import { Grid, Typography } from "@material-ui/core";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import { loadArchitects } from "features/architects/architectsSlice";
import { loadContractors } from "features/contractors/contractorsSlice";
import { loadProviderMaterialsAll } from "features/providers/providerMaterialsSlice";
import { loadProviders } from "features/providers/providersSlice";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { DragDropContext } from "react-beautiful-dnd";

import { container } from "assets/jss/commons";
import { makeStyles } from "@material-ui/styles";
import ArchitectResults from "./sections/ArchitectResults";
import ProviderResults from "./sections/ProviderResults";
import ContractorResults from "./sections/ContractorResults";
import FilteringSection from "./sections/FilteringSection";
import MaterialResults from "./sections/MaterialResults";
import SelectedSection from "./sections/SelectedSection";
import { move } from "components/DNDElement/DNDElement";


import profile1 from "assets/img/faces/christian.jpg";
import profile2 from "assets/img/faces/avatar.jpg";
import profile4 from "assets/img/faces/card-profile1-square.jpg";

const useStyles = makeStyles((theme) => ({
  container: {
    zIndex: "12",
    color: "#FFFFFF",
    paddingTop: "200px",
    marginLeft: "1rem",
    position: "absolute",
    ...container,

    paddingLeft: "200px",
  },
  main: {
    background: "#FFFFFF",
    // paddingBottom: "20px",
    position: "relative",
    zIndex: "3",
  },
  mainRaised: {
    margin: "-60px 30px 60px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  },
}));

export default function RecommendationsPage(props) {

  const architectInitial = [
    {
      id: "sdfs",
      name: "Dagim Temesgen",
      image: profile1,
      user_type:'architect',
      type:'GAR',
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "sdfdss",
      name: "Amen Temesgen",
       user_type:'architect',
      type:'PPAR',
      image: profile2,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "sdsdfs",
      name: "Abenezer Zeleke", 
      user_type:'architect',
      type:'PAR',
      image: profile4,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
  ];

  const contractorInitial = [
    {
      id: "ewrw",
      name: "Dagim Temesgen",
      image: profile1,
      user_type:'contractor',
      type:['Civil', 'Mechanical'],
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "fsdffs",
      name: "Amen Temesgen",
       user_type:'contractor',
       type:['Electrical', 'Mechanical'],
      image: profile2,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "sfasda",
      name: "Abenezer Zeleke", 
      user_type:'contractor',
      type:[ 'Electrical'],
      image: profile4,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
  ];

  const providerInitial = [
    {
      id: "sdfsfsafa",
      name: "Dagim Temesgen",
      image: profile1,
      user_type:'provider',
      type:['Civil', 'Mechanical'],
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "vzcvzx",
      name: "Amen Temesgen",
       user_type:'provider',
       type:['Electrical', 'Mechanical'],
      image: profile2,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
    {
      id: "fdhfgghf",
      name: "Abenezer Zeleke", 
      user_type:'provider',
      type:[ 'Electrical'],
      image: profile4,
      description:
        "You can write here details about one of your team members. You can give more details about what they do. ",
    },
  ];
  
  

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadArchitects());
    dispatch(loadProviders());
    dispatch(loadContractors());
    dispatch(loadProviderMaterialsAll());
  }, []);

  const {providerMaterials} = useSelector(state => state.providerMaterials)

  const [architectItems, setArchitectItems] = useState(architectInitial);
  const [providerItems, setProviderItems] = useState(providerInitial);
  const [contractorItems, setContractorItems] = useState(contractorInitial);
  const [materialItems, setMaterialItems] = useState(providerMaterials);

  const [selected, setSelected] = useState([]);

  const id2List = {
    architectDroppable: "architectDroppable",
    providerDroppable: "providerDroppable",
    contractorDroppable: "contractorDroppable",
    materialDroppable: "materialDroppable",
    selectedDroppable: "selectedDroppable",
  };

  const onDragEnd = (result) => {
    const { source, destination } = result;

    // dropped outside the list
    if (!destination) {
      return;
    }
    if (source.droppableId === destination.droppableId) {
      return;
    }

    if (
      (source.droppableId === "architectDroppable" &&
        destination.droppableId === "selectedDroppable") 
    ) {
      const result = move(architectItems, selected, source, destination);
      console.log( result);
      setArchitectItems(result.architectDroppable);
      setSelected(result.selectedDroppable);
    }
    if (
      (source.droppableId === "providerDroppable" &&
        destination.droppableId === "selectedDroppable") 
    ) {
      const result = move(providerItems, selected, source, destination);
      console.log('provider moved');
      setProviderItems(result.providerDroppable);
      setSelected(result.selectedDroppable);
    }
    if (
      (source.droppableId === "contractorDroppable" &&
        destination.droppableId === "selectedDroppable") 
    ){
      const result = move(contractorItems, selected, source, destination);
      console.log('contractor moved');
      setContractorItems(result.contractorDroppable);
      setSelected(result.selectedDroppable);
    }
    if (
      (source.droppableId === "materialDroppable" &&
        destination.droppableId === "selectedDroppable") 
    ){
      const result = move(materialItems, selected, source, destination);
      console.log('material moved');
      setMaterialItems(result.materialDroppable);
      setSelected(result.selectedDroppable);
    }
  };

  return (
    <React.Fragment>
      <Header
        color="dark"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        // fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
      />
      <Grid container justify="flex-start">
        <Grid item md={2} style={{ height: "90vh", overflow: "auto" }}>
          <FilteringSection />
        </Grid>
        <DragDropContext onDragEnd={onDragEnd}>
          <Grid item md={6} style={{ height: "90vh", overflow: "auto" }}>
            <Typography
              style={{
                fontStyle: "italic",
                marginLeft: 40,
                fontWeight: "bold",
              }}
            >
              Search Results
            </Typography>
            <ArchitectResults architects={architectItems} />
            <ContractorResults contractors={contractorItems} /> 
            <ProviderResults providers={providerItems} />
           
            <MaterialResults history={props.history} providerMaterials={materialItems} />
          </Grid>
          <Grid item md={4} style={{marginTop:2, backgroundColor:'#ebecf0'}}>
            <SelectedSection 
            items={selected}
            architects={architectItems}
                providers={providerItems}
                contractors={contractorItems} 
                providerMaterials={materialItems}

                setArchitectItems={setArchitectItems}
                setContractorItems={setContractorItems}
                setProviderItems={setProviderItems}
                setMaterialItems={setMaterialItems}
                setSelected={setSelected}

            />
          </Grid>
        </DragDropContext>
      </Grid>
      {/* </div> */}
    </React.Fragment>
  );
}
