import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const contractorDesignsSlice = createSlice({
    name: 'contractorDesigns',
    initialState: {
        contractorDesigns : null
    },
    reducers: {
        loadContractorDesignsSuccess: (state, action) => {
            state.contractorDesigns=action.payload
        }
    }
})

export default contractorDesignsSlice.reducer;

const {loadContractorDesignsSuccess} = contractorDesignsSlice.actions;

export const loadContractorDesigns = (contractorId) => async dispatch => {
    try {
        const res = await api.get('/contractorDesigns?contractor_id='+contractorId);
        console.log("contractors Designs",res.data)
        dispatch(loadContractorDesignsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const addContractorDesign = (values, contractorId,userId,callback) => async dispatch => {
    const val= {...values,
        contractor_id:contractorId,
        user_id:userId
    }
    try {
       await api.post('/contractorDesigns',val);
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}

export const editContractorDesign = (designId,values,callback) => async dispatch => {
    
    try {
        await api.patch('/contractorDesigns/'+designId,values );
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}