import React, { useState } from "react";

import studio1 from "assets/img/backgrounds/landing-bg.jpg";
import { useDispatch, useSelector } from "react-redux";
import {
  Dialog,
  IconButton,
  makeStyles,
  useTheme,
  Grid,
  Button,
  CardActionArea,fade, Slide, DialogTitle
} from "@material-ui/core";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { Close, Edit } from "@material-ui/icons";
import { Field, reduxForm } from "redux-form";
import { renderTextField } from "components/ReduxInput/ReduxInput";

import {
  loadProviderMaterials,
  editProviderMaterial,
} from "features/providers/providerMaterialsSlice";
import { container } from "assets/jss/commons";

import material1 from 'assets/img/materials/material1.jpg'
import material2 from 'assets/img/materials/material2.jpg'
import material3 from 'assets/img/materials/material3.jpg'
import material4 from 'assets/img/materials/material4.jpg'
import material5 from 'assets/img/materials/material5.jpg'
import { Link } from "react-router-dom";

const imageList = [
  material1,material2,material3,material4,material5
];


const useStyles = makeStyles((theme) => ({
  container: {
    ...container,
  },
  root: {
    display: "flex",
    marginTop: "20px",
    marginBottom: "40px",
    marginRight: "100px",

    backgroundColor: theme.palette.grey[900],
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: "30%",
    height: "auto",
    minHeight: 300,
    minWidth: 300,
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  editButton: {
    color: "black",
    position:'absolute',
    right:10,
    bottom:25,
    zIndex:1302
  },
  editModal: {
    width: "55%",
    padding: 50,
  },

  textField: {
    margin: 20,
    marginTop: "20px",
    width: "80%",
  },
  root: {
    maxWidth: 345,
    height: 340,
    backgroundColor: fade("#FFF", 0.9),
    textDecoration:'none'
  },
  media: {
    height: 140,
  },
  rootSelected: {
    height: 345,
    backgroundColor: fade("#FFF", 0.15),
  },
  cardFooter: {
    height: 200,
    overflow: "auto",
  },
  cardContainer: {
    padding: 10,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});


const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let EditMaterialForm = reduxForm({
  form: "editMaterial",
});

EditMaterialForm = (props) => {
  const { handleSubmit, pristine, reset, submitting, classes } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Grid container justify="center">
        <Grid container justify="center">
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="name"
              component={renderTextField}
              label="Material Name"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="category"
              component={renderTextField}
              label="Material Category"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="price"
              component={renderTextField}
              label="Price"
              className={classes.textField}
              type='number'
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="location"
              component={renderTextField}
              label="Location"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About the Material"
              multiline={true}
              variant="outlined"
            />
          </Grid>
         
        </Grid>
          <Button type="submit" color="primary" size="large">
            Update Material
          </Button>
        </Grid>
      </form>
    </div>
  );
};

EditMaterialForm = reduxForm({
  form: "createProject",
})(EditMaterialForm);

const ProviderMaterialsSection = (props) => {
  const { ...rest } = props;

  const classes = useStyles();

  const dispatch = useDispatch();

  const { providerMaterials } = useSelector((state) => state.providerMaterials);
  const { user } = useSelector((state) => state.user);

  const [selectedMaterial, setSelectedMaterial] = useState(null);

  const [openEditMaterial, setOpenEditMaterial] = useState(false);


  const handleOpenEditModal = () => {
    setOpenEditMaterial(true);
  };
  const handleCloseEditModal = (value) => {
    setOpenEditMaterial(false);
  };

  const EditSuccessful = () => {
    dispatch(loadProviderMaterials(user.id));
    setOpenEditMaterial(false);
  };

  const submit = (values) => {
    dispatch(
      //  (values,user.id, ()=>setMaterialModal(false))
      editProviderMaterial(selectedMaterial, values, () => EditSuccessful())
    );
  };
const RenderMaterial=(props)=>{
  const {material, index,history}=props;
  return(
    <Grid
            key={material.id}
            item
            md={3}
            className={classes.cardContainer}
          >
             
            <Card
              className={classes.root}
          
            >
              <CardActionArea>
                <CardMedia
                  className={classes.media }
                  image={imageList[index%5]}
                  title={material.name}
                  component={Link}
                  to={`/materialDescription/${material.id}`}
                >
                </CardMedia>

                <CardContent className={classes.cardFooter}>
   
                      
                  <Typography gutterBottom variant="h5" component="h2">
                    {material.name}
                  </Typography>
                  
                  <Typography
                    variant="body2"
                    color="primary"
                    component="p"
                  >
                    Price: {material.price}ETB
                  </Typography>
                  <Typography
                    variant="body2"
                    
                    component="p"
                  >
                    {material.location}
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                  >
                    {material.deliverable}
                  </Typography>
                  <div style={{marginTop:10}} />
                  <Typography
                    variant="body2"
                    color="primary"
                    component="p"
                  >
                    Description: 
                  </Typography>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    component="p"
                    style={{display:'inline-block'}}
                  >
                    {material.description}
                  </Typography>
                {user.id === material.provider_id ? 
                         
                         <IconButton
                           className={classes.editButton}
                           onClick={handleOpenEditModal}
                           onClick={() => {
                             setSelectedMaterial(material.id);
                             handleOpenEditModal();
                           }}
                         >
                           <Edit />
                         </IconButton>
                       :
                       null}
                </CardContent>
                
              </CardActionArea>
            </Card>
          </Grid>
  )
}
  return (
    <React.Fragment>
 <Dialog
                onClose={handleCloseEditModal}
                aria-labelledby="simple-dialog-title"
                open={openEditMaterial}

        TransitionComponent={Transition}
                maxWidth="lg"
                classes={{
                  root: classes.center,
                  paper: classes.modal,
                }}
              >
                <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Grid container justify="space-between">
            <Grid item>
              <h4 className={classes.modalTitle}>Edit Material</h4>
            </Grid>
            <Grid item>
              <IconButton
                className={classes.modalCloseButton}
                key="close"
                aria-label="Close"
                color="inherit"
                onClick={handleCloseEditModal}
              >
                <Close className={classes.modalClose} />
              </IconButton>
            </Grid>
          </Grid>
        </DialogTitle>
                <EditMaterialForm onSubmit={submit} classes={classes} />
              </Dialog>
      <div className={classes.container}> 
        <Grid container justify='flex-start'>
        {providerMaterials !== null ? (
          providerMaterials.map((material,index) => (
            <React.Fragment key={material.id}>
              

             
             
            <RenderMaterial material={material} index={index} history={props.history} />


            
            </React.Fragment>
          ))
        ) : (
          <div>No Materials Found</div>
        )}
        </Grid>
      </div>
    </React.Fragment>
  );
};
export default ProviderMaterialsSection;
