import { applyMiddleware, combineReducers, createStore } from '@reduxjs/toolkit';
import user from 'features/auth/userSlice';


import architects from 'features/architects/architectsSlice';
import architectDetails from 'features/architects/architectDetailsSlice';
import architectDesigns from 'features/architects/architectDesignsSlice';

import providers from 'features/providers/providersSlice';
import providerDetails from 'features/providers/providerDetailsSlice';
import providerMaterials from 'features/providers/providerMaterialsSlice';
import materialDetails from 'features/providers/materialDetailsSlice';

import contractors from 'features/contractors/contractorsSlice';
import contractorDetails from 'features/contractors/contractorDetailsSlice';
import contractorDesigns from 'features/contractors/contractorDesignsSlice';

import projects from 'features/projects/projectsSlice';

import thunk from 'redux-thunk';

import { reducer as formReducer } from 'redux-form'

import { composeWithDevTools } from 'redux-devtools-extension';

const reducer = combineReducers({
    user,
    architects,
    architectDetails,
    architectDesigns,

    providers,
    providerDetails,
    providerMaterials,
    materialDetails,

    contractors,
    contractorDetails,
    contractorDesigns,

    projects,

    form:formReducer
})

const store = createStore(
  reducer,
  {},
  composeWithDevTools(applyMiddleware(thunk))
  
)

export default store;
