import {
  Button,
  Divider,
  Grid,
  IconButton,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";

import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import "@date-io/date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import { itemPrices } from "assets/data";
import { Check } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  buttonStyle: {
    // position: "absolute",
    // right: 50,
    // top: 20,
  },

  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  formItem: {
    margin: 20,
  },
  divider: {
    marginTop: 40,
  },
  listHeader: {
    backgroundColor: theme.palette.primary.main,
  },
}));

export default function InsertData() {
  const [selectedDate, setSelectedDate] = React.useState(new Date(Date.now()));

  const [data, setData] = useState([]);

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const classes = useStyles();
  const [state, setState] = useState({
    item: "",
    price: null,
    date: selectedDate,
  });

  const handleNameChange = (event) => {
    // const name = event.target.name;
    setState({
      ...state,
      item: event.target.value,
    });
  };
  const handlePriceChange = (event) => {
    // const price = event.target.value;
    setState({
      ...state,
      price: event.target.value,
    });
  };

  const updateData = () => {
    setData([...data, state]);
  };

  

  return (
    <React.Fragment>
      <Button
        className={classes.buttonStyle}
        variant="outlined"
        color="primary"
        onClick={handleClickOpen}
      >
        Insert Data
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
        maxWidth="md"
      >
        <DialogTitle id="form-dialog-title">Insert Data</DialogTitle>
        <DialogContent>
          <div>
            <FormControl className={classes.formControl}>
              <Grid container justify="center" alignItems="center">
                <Grid item className={classes.formItem}>
                  <InputLabel htmlFor="age-native-simple">Item</InputLabel>

                  <Select
                    native
                    value={state.item}
                    onChange={handleNameChange}
                    inputProps={{
                      name: "Item",
                      id: "item-native-simple",
                    }}
                  >
                    {itemPrices.map((item) => (
                      <option value={item.name}>{item.name}</option>
                    ))}
                  </Select>
                </Grid>
                <Grid item className={classes.formItem}>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="price"
                    label="Price"
                    fullWidth
                    type="number"
                    value={state.price}
                    onChange={handlePriceChange}
                  />
                </Grid>
                <Grid item className={classes.formItem}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <KeyboardDatePicker
                      margin="normal"
                      id="date-picker-dialog"
                      label="Date picker dialog"
                      format="MM/dd/yyyy"
                      value={selectedDate}
                      onChange={handleDateChange}
                      KeyboardButtonProps={{
                        "aria-label": "change date",
                      }}
                    />
                  </MuiPickersUtilsProvider>
                </Grid>
                <Grid item>
                  <IconButton onClick={() => updateData()}>
                    <Check
                      style={{
                        color: "white",
                        backgroundColor: "green",
                        borderRadius: "50%",
                        padding: 5,
                      }}
                    />
                  </IconButton>
                </Grid>
              </Grid>
            </FormControl>
          </div>
          <Divider className={classes.divider} />
          {data.length > 0 ? (
            <div style={{ marginBottom: 40 }}>
              <Grid
                container
                justify="flex-start"
                className={classes.listHeader}
              >
                <Grid item xs={4}>
                  <Typography variant="h5">Item</Typography>
                </Grid>
                <Grid item xs={4}>
                  <Typography variant="h5">Item</Typography>
                </Grid>
                <Grid item xs={4}>
                  <Typography variant="h5">Date</Typography>
                </Grid>
              </Grid>

              {data.map((d) => (
                <React.Fragment>
                  <Grid container justify="flex-start">
                    <Grid item xs={4}>
                      <Typography variant="body">{d.item}</Typography>
                    </Grid>
                    <Grid item xs={4}>
                      <Typography variant="body">{d.price}</Typography>
                    </Grid>
                    <Grid item xs={4}>
                      <Typography variant="body">
                        {d.date.toDateString()}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Divider />
                </React.Fragment>
              ))}
            </div>
          ) : null}

          <div></div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleClose} color="primary">
            Submit
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}
