export default function ArchitectResults(props) {
  const {architects} = props;

  const classes = useStyles();

  const RenderDragElement = (props) => {
    const {architect} = props;
    return (
      <Grid container spacing={2} alignItems='center' style={{marginTop:2,marginBottom:2}}>
        <Grid item>
          <DragIndicatorIcon style={{fontSize:50}} />
        </Grid>
        <Grid item>
          <img src={architect.image} width={100} height={100} style={{borderRadius:'50%'}} />
        </Grid>
        <Grid item md={7}>
          <Grid direction='column' container>
            <Typography variant='h5' color='primary'>{architect.name}</Typography>
          </Grid>
          <Grid direction='column' container>
            <Typography>{architect.description}</Typography>
          </Grid>
        </Grid>
        <Grid item>
          <Typography color='textSecondary'>{architect.type}</Typography>
          
        </Grid>
      </Grid>
    );
  };

  if (architects) {
    return (
      <Grid container justify="center">
        <Droppable droppableId="architectDroppable">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
            >
              <Typography variant="h5" color="primary" style={{ marginLeft: 20 }}>
       Architect Results
      </Typography>
              {architects.length > 0 ? (
                architects.map((architect, index) => (
                  <Draggable
                    key={architect.id}
                    draggableId={architect.id}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                         
                        <RenderDragElement architect={architect} />
                       
                      </div>
                    )}
                  </Draggable>
                ))
              ) : (
                <div style={{ height: 50 }}>No Architects</div>
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>

       
      </Grid>
    );
  } else {
    return <Typography variant="h3"> </Typography>;
  }
}
