import { Button, Grid, IconButton, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import React from "react";
import PinDropIcon from '@material-ui/icons/PinDrop';
import profile1 from "assets/img/faces/christian.jpg";

import netsaDesign from "assets/img/Netsa_Design.png";
import { Facebook, LocationCity, Twitter, LinkedIn, Message } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  sectionContainer: {
    backgroundColor: "#ebf6fa",
    marginTop: -20,
  },
  imageContainer: {
    padding: "50px",
  },
  profileImage: {
    borderRadius: "50%",
    height: "200px",
    width: "200px",
  },
  imageTagContainer: {
    backgroundColor: "#95189d",
    marginTop: -30,
    padding: "20px",
    borderRadius: "50%",
  },
  imageTag: {
    color: "white",
  },
  descriptionsContainer: {
    marginTop: 40,
  },
  userName: {
    fontWeight: "bold",
  },
  userTagLine: {
    marginTop: "1rem",
    textAlign: "flex-start",
    padding: "0",
    marginBottom: "1rem",
  },
  userTags: {
    backgroundColor: "#d4d6da",
    maxWidth: "50px",
    marginRight: "20px",
    marginTop: "0",
    // width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    // height: "100%",
    // lineHeight: "41px",
    fontSize: 15,
    color: "black",
    paddingLeft: 10,
    paddingRight: 10,
    paddingTop: 1,
    paddingBottom: 1,
    borderRadius: 3,
  },
  activeTag: {
    backgroundColor: "#efe5da",
  },
  socialLine: {
    marginTop: "1rem",
    textAlign: "flex-start",
    padding: "0",
  },
  companyName: {
    fontWeight: "bold",
  },
  locationIcon: {
    fontSize: 20,
  },
  socialIcons: {
    maxWidth: "24px",
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
    color: theme.palette.primary.main,
  },
  socialLine: {
    marginTop: "1rem",
    textAlign: "flex-start",
    padding: "0",
    marginBottom:'2rem'
  },
  netsaDesign:{
      marginTop:10,
      paddingRight:100,
    height:'400px',
    width:'700px'
  },
  socialsContainer:{
      paddingTop:'3rem'
  },
  messageButton:{
      padding:'1rem',
      backgroundColor:'#fcfcfc',
      paddingRight:20,
      paddingLeft:20
  }
}));

export default function DescriptionSection(props) {
  const { user } = props;
  const classes = useStyles();
  return (
    <React.Fragment>
      <Grid container justify="center" className={classes.sectionContainer}>
        <Grid item md={4}>
          <div className={classes.imageContainer}>
            <div>
              <img src={profile1} className={classes.profileImage} />
            </div>
            {/* <div className={classes.imageTagContainer}>
                               <Typography className={classes.imageTag}>2345 followers</Typography>
                           </div> */}
          </div>
        </Grid>
        <Grid item md={4} className={classes.descriptionsContainer}>
          <Typography variant="h4" className={classes.userName}>
            {user.name}
          </Typography>
          <div>
            <Grid container className={classes.userTagLine}>
              <Grid item>
                <p className={classes.userTags + " " + classes.activeTag}>
                  {" "}
                  tag
                </p>
              </Grid>
              <Grid item>
                <p className={classes.userTags}> tag</p>
              </Grid>
              <Grid item>
                <p className={classes.userTags}> tag</p>
              </Grid>
            </Grid>
            <Grid container>
              <Grid item>
                <Typography variant="caption">Founder and CEO at:</Typography>
              </Grid>
              <Grid item>
                <Typography variant="caption" className={classes.companyName}>
                  {" "}
                  NIDF
                </Typography>
              </Grid>
            </Grid>
            <div style={{ margin: 5 }} />
            <Grid container>
              <Grid item>
                <PinDropIcon className={classes.locationIcon} />
              </Grid>
              <Grid item>
                <Typography variant="caption" className={classes.companyName}>
                  {" "}
                  Addis Ababa, Ethiopia
                </Typography>
              </Grid>
            </Grid>
          </div>
        </Grid>
        <Grid item md={4} className={classes.socialsContainer}>
            {/* <img src={netsaDesign} className={classes.netsaDesign}/> */}

        <Grid container direction='column' justify='space-around'>
            <Grid item>
            <div className={classes.socialLine}>
                <IconButton>
                  <Twitter color="primary" className={classes.socialIcons} />
                </IconButton>
                <IconButton>
                  <Facebook color="secondary" className={classes.socialIcons} />
                </IconButton>
                <IconButton>
                  <LinkedIn color="error" className={classes.socialIcons} />
                </IconButton>
                <IconButton>
                  <LinkedIn color="primary" className={classes.socialIcons} />
                </IconButton>
                <IconButton>
                  <LinkedIn color="primary" className={classes.socialIcons} />
                </IconButton>
              </div>
            </Grid>
            <Grid item>
                <Grid container>
                    <Grid item>
                        <Button startIcon={<Message />} className={classes.messageButton}>Send Message</Button>   
                    </Grid>
                </Grid>
                
            </Grid>
        </Grid>

         
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
