import { Button, Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import React from "react";

import { fade } from "@material-ui/core/styles";

import profile1 from "assets/img/faces/christian.jpg";
import profile2 from "assets/img/faces/avatar.jpg";
import profile3 from "assets/img/faces/card-profile1-square.jpg";
import profile4 from "assets/img/faces/card-profile2-square.jpg";
import { CropRotate } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  imageBackground: {
    // width: "100%",
    height: 400,
  },
  filter: {
    height: "100%",
    width: "100%",
    backgroundColor: "#000000de",
  },
  icon: {
    fontSize: 100,
    marginBottom: 20,
  },
  name: {
    //   color:'yellow',
    marginBottom: 20,
    textAlign: "center",
    color: "white",
  },
  description: {
    marginLeft: 10,
    marginRight: 10,
    marginBottom: 20,
    color: "white",
    fontSize: 15,
    textAlign: "center",
  },
  learnMore: {
    backgroundColor: fade("#FFF", 0.35),
    paddingTop: 10,
    paddingBottom: 10,
    paddingLeft: 5,
    paddingRight: 5,
    width: "65%",
    marginLeft: "17%",
    justifyContent: "center",
    color: "white",
  },
  gridItem: {
    width: "100%",
  },
}));

const team = [
  {
    name: "Dagim",
    image: profile1,
    description:
      "You can write here details about one of your team members. You can give more details about what they do. ",
  },
  {
    name: "Abenezer",
    image: profile2,
    description:
      "You can write here details about one of your team members. You can give more details about what they do. ",
  },
  {
    name: "Tinsae",
    image: profile3,
    description:
      "You can write here details about one of your team members. You can give more details about what they do. ",
  },
  {
    name: "Amen",
    image: profile4,
    description:
      "You can write here details about one of your team members. You can give more details about what they do. ",
  },
];

export default function TeamSection() {
  const classes = useStyles();
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      spacing={2}
      style={{ marginTop: 50, marginBottom: 100 }}
    >
      <Grid item>
        <Typography variant="h2" className={classes.title}>
          Teams
        </Typography>
      </Grid>
      <Grid item>
        <Grid container justify="center" spacing={2}>
          {team.map((member) => (
            <Grid item md={2}>
              <div
                style={{
                  backgroundImage: "url(" + member.image + ")",
                  backgroundSize: "cover",
                  backgroundPosition: "top center",
                }}
                className={classes.imageBackground}
              >
                <div className={classes.filter}>
                  <Grid
                    container
                    direction="column"
                    justify="center"
                    alignContent="center"
                    style={{ paddingTop: 50 }}
                  >
                    <Grid item>
                      <CropRotate
                        color="secondary"
                        // className={classes.}
                        className={classes.icon + " " + classes.gridItem}
                      />
                    </Grid>
                    <Grid item className={classes.gridItem}>
                      <Typography variant="h4" className={classes.name}>
                        {member.name}
                      </Typography>
                      <Typography className={classes.description}>
                        {member.description}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Button className={classes.learnMore}>More</Button>
                    </Grid>
                  </Grid>
                </div>
              </div>
            </Grid>
          ))}
        </Grid>
      </Grid>
    </Grid>
  );
}
