import {
  Box,
  Card,
  Grid,
  IconButton,
  makeStyles,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import { ExpandLess, ExpandMore } from "@material-ui/icons";
import GraphComponent from "./GraphComponent";

const useStyles = makeStyles((theme) => ({
  listItem: {
    backgroundColor: "white",
  },
  cardContainer: {
    marginTop: 5,
    paddingTop: 5,
    paddingBottom: 5,
  },
  itemText: {
    fontWeight: "bold",
  },
  changePositive: {
    color: "green",
  },
  changeNegative: {
    color: "red",
  },
}));

// const data = [
//   { x: 0, y: 8 },
//   { x: 1, y: 5 },
//   { x: 2, y: 4 },
//   { x: 3, y: 9 },
//   { x: 4, y: 1 },
//   { x: 5, y: 7 },
//   { x: 6, y: 6 },
//   { x: 7, y: 3 },
//   { x: 8, y: 2 },
//   { x: 9, y: 0 },
// ];

export default function ListComponent(props) {
  const classes = useStyles();

  const { index, item } = props;
  const toggleDetail = () => {
    setShowDetail(!showDetail);
  };

  const [showDetail, setShowDetail] = useState(false);

  return (
    <React.Fragment>
      <Grid item sm={12}>
        <Card onClick={() => toggleDetail()} className={classes.cardContainer}>
          <Grid container>
            <Grid item xs={6} sm={3} className={classes.listItem}>
              <Box width={30} style={{display:'inline-block', marginLeft:20}}>
                <Typography>
                {index + ". "}
                </Typography>
              </Box>
              <Typography style={{display:'inline-block'}} className={classes.listText}>
             
                {item.name}
              </Typography>
            </Grid>
            <Grid item xs={6} sm={3} className={classes.listItem}>
              <Typography className={classes.listText}>{item.price}</Typography>
            </Grid>
            <Grid item xs={6} sm={3} className={classes.listItem}>
              <Typography
                className={
                  item.change >= 0
                    ? classes.changePositive + " " + classes.itemText
                    : classes.changeNegative + " " + classes.itemText
                }
              >
                {item.change}
              </Typography>
            </Grid>
            <Grid item xs={6} sm={2} className={classes.listItem}>
              <Typography className={classes.listText}>
                {item.Availability}
              </Typography>
            </Grid>
            <Grid item xs={6} sm={1} className={classes.listItem}>
              <IconButton onClick={() => toggleDetail()} style={{ padding: 0 }}>
                {showDetail ? (
                  <ExpandLess color="primary" />
                ) : (
                  <ExpandMore color="primary" />
                )}
              </IconButton>
            </Grid>
          </Grid>

          {showDetail ? (
            <Grid item xs={12} sm={12}>
              {/* <XYPlot  height={300} width= {700} >
 <VerticalGridLines />
  <HorizontalGridLines />
 <XAxis/>
  <YAxis />
  <LineMarkSeries color='red' colorDomain='green' style={{backgroundColor:'green'}}  data={data} />
</XYPlot> */}
              <GraphComponent />
            </Grid>
          ) : null}
        </Card>
      </Grid>
    </React.Fragment>
  );
}
