import React from "react";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  makeStyles,
  Slide,
} from "@material-ui/core";

import { Add, Close } from "@material-ui/icons";

import { Field, reduxForm } from "redux-form";

import { renderTextField } from "components/ReduxInput/ReduxInput";
import { useDispatch, useSelector } from "react-redux";
import { addProviderMaterial } from "features/providers/providerMaterialsSlice";
import { loadProviderMaterials } from "features/providers/providerMaterialsSlice";

import ImageUploader from "components/ImageUpload/ImageUploader";


const useStyles = makeStyles((theme) => ({
  modal: {
    width: "55%",
    padding: 50,
  },

  textField: {
    margin: 20,
    marginTop: "20px",
    width: "80%",
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let AddMaterialForm = reduxForm({
  form: "addMaterial",
});

AddMaterialForm = (props) => {
  const { handleSubmit, pristine, reset, submitting, classes } = props;

  // const [aboutNumber, setAboutNumber] = useState(1);
  console.log()



  return (
    <div>
      <form onSubmit={handleSubmit}>

    <Grid container>
      <Grid item  md={8}>
        <Grid container justify="center">
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="name"
              component={renderTextField}
              label="Material Name"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="category"
              component={renderTextField}
              label="Material Category"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="price"
              component={renderTextField}
              label="Price"
              className={classes.textField}
              type='number'
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="location"
              component={renderTextField}
              label="Location"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About the Material"
              multiline={true}
              variant="outlined"
            />
          </Grid>
          
        </Grid>
      </Grid>
      <Grid
                item
                md={3}
                justify="center"
                alignContent="center"
                alignItems="center"
              >
                <ImageUploader />
              </Grid>
    </Grid>

        
        <Button type="submit" color="primary" size="large">
            Add Material
          </Button>
      </form>
    </div>
  );
};

AddMaterialForm = reduxForm({
  form: "createProject",
})(AddMaterialForm);

export default function AddMaterialModal(props) {
  const classes = useStyles();
  const [materialModal, setMaterialModal] = React.useState(false);

  const providerDetails = useSelector((state) => state.providerDetails);
  const { user } = useSelector((state) => state.user);

  const dispatch = useDispatch();
  const submit = (values) => {
    const val={...values,rating:3, number_of_ratings:4657,answered_question_no:564,user_type: "material",
      aboutItem: [
        "Share videos, photos, and music from your Apple device using AirPlay.",
        "Wireless that goes the distance: Basement rec room? Backyard movie night? Bring them on. The long-range wireless receiver gives you extended range and a stronger signal for smooth streaming even in rooms farther from your router",
        "Brilliant picture quality: Experience your favorite shows with stunning detail and claritywhether youre streaming in HD, 4K, or HDR, youll enjoy picture quality thats optimized for your TV with sharp resolution and vivid color",
        "Tons of power, tons of fun: Snappy and responsive, youll stream your favorites with easefrom movies and series on Apple TV, Prime Video, Netflix, to cable alternatives like Sling, enjoy the most talked-about TV across thousands of channels",
        "No more juggling remotes: Power up your TV, adjust the volume, mute, and control your streaming all with 1 remoteuse your voice to quickly search across channels, turn captions on, and more in a touch",
        "Setup is a cinch: Plug it in, connect to the internet, and start streamingits that simple",
        "Private listening on mobile: Use the free Roku mobile app to pump up the volume on your shows without disturbing the house",
        "Endless entertainment: Stream what you love, including free TV, live news, sports, and more; Never miss the most talked-about events, award-winning shows, latest blockbuster hits, and moreIts easy to stream what you love and cut back on cable bills",
      ]}
    dispatch(
      addProviderMaterial(
        val,
        providerDetails.providerDetails[0].id,
        user.id,
        () => {
          dispatch(
            loadProviderMaterials(providerDetails.providerDetails[0].id)
          );
          setMaterialModal(false);
        }
      )
    );
  };

  return (
    <React.Fragment>
      {providerDetails !== null &&
      providerDetails.providerDetails !== null &&
      user.id === providerDetails.providerDetails[0].user_id ? (
        <React.Fragment>
          <Grid container justify="center">
            <Grid item>
              <Button
                variant="outlined"
                color="primary"
                onClick={() => setMaterialModal(true)}
              >
                <Add color="primary" fontSize="default" />
                Add Material
              </Button>
            </Grid>
          </Grid>

          <Dialog
            classes={{
              root: classes.center,
              paper: classes.modal,
            }}
            open={materialModal}
            maxWidth="lg"
            TransitionComponent={Transition}
            keepMounted
            onClose={() => setMaterialModal(false)}
            aria-labelledby="classic-modal-slide-title"
            aria-describedby="classic-modal-slide-description"
          >
            <DialogTitle
              id="classic-modal-slide-title"
              disableTypography
              className={classes.modalHeader}
            >
              <Grid container justify="space-between">
                <Grid item>
                  <h4 className={classes.modalTitle}>Add Your Material</h4>
                </Grid>
                <Grid item>
                  <IconButton
                    className={classes.modalCloseButton}
                    key="close"
                    aria-label="Close"
                    color="inherit"
                    onClick={() => setMaterialModal(false)}
                  >
                    <Close className={classes.modalClose} />
                  </IconButton>
                </Grid>
              </Grid>
            </DialogTitle>
            <DialogContent
              id="classic-modal-slide-description"
              className={classes.modalBody}
            >
              <AddMaterialForm onSubmit={submit} classes={classes} />
            </DialogContent>
          </Dialog>
        </React.Fragment>
      ) : null}
    </React.Fragment>
  );
}
