import {
    Card,
    Grid,
    makeStyles,
  } from "@material-ui/core";
  import Header from "components/Header/Header";
  import HeaderLinks from "components/Header/HeaderLinks";
  import React from "react";
  import { container } from "assets/jss/commons";
  import { items } from "assets/data";
  import Parallax from "components/Parallax/Parallax";
  import classNames from "classnames";
  
  import backgroundImage from "assets/img/backgrounds/bg.jpg";
import ItemsList from "./sections/ItemsList";
  
  const useStyles = makeStyles((theme) => ({
    container: {
      zIndex: "12",
      color: "#FFFFFF",
      paddingTop: "100px",
      // marginLeft: "1rem",
      // ...container,
    },
    main: {
      background: "#FFFFFF",
      position: "relative",
      zIndex: "3",
    },
    mainRaised: {
      margin: "-60px 30px 60px",
      borderRadius: "6px",
      boxShadow:
        "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
    }
  }));
  
  export default function ProvidersItemsPages(props) {
  
    const classes = useStyles();
  
    return (
      <React.Fragment>
        <Header
          color="transparent"
          brand="NIDF"
          rightLinks={<HeaderLinks />}
          fixed
          changeColorOnScroll={{
            height: 400,
            color: "white",
          }}
        />
        <Parallax small filter image={backgroundImage} />
        <div className={classNames(classes.main, classes.mainRaised)}>
         
            <ItemsList />
   
        </div>
      </React.Fragment>
    );
  }
  