import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const contractorsSlice = createSlice({
    name: 'contractors',
    initialState: {
        contractors : null
    },
    reducers: {
        addContractorsSuccess: (state, action) => {
            state.contractors=action.payload
        }
    }
})

export default contractorsSlice.reducer;

const {addContractorsSuccess} = contractorsSlice.actions;

export const loadContractors = () => async dispatch => {
    try {
        const res = await api.get('/contractors');
        dispatch(addContractorsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const addContractor = (values, userId,callback) => async dispatch => {
    const val= {...values,
        user_id:userId
    }
    try {
       await api.post('/contractors',val);
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}
