import { makeStyles } from "@material-ui/styles";
import React from "react";
import { container } from "assets/jss/commons";
import { Card, CardContent, Grid, Typography } from "@material-ui/core";
import { Camera, TrendingDown } from "@material-ui/icons";
import TrendingDownIcon from '@material-ui/icons/TrendingDown';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  sectionContainer: {
    // ...container,
    backgroundColor: "#f4f7fc",
    paddingLeft: 50,
    paddingRight: 50,
    paddingTop: 50,
    paddingBottom:50
  },
  title: {
    fontWeight: "bold",
  },
  projectContainer:{
    textDecoration:'none'
  },
  projectTitle: {},
  projectsContainer:{
    marginTop:10,
    
  },
  projectProgress: {
    marginTop: 10,
    marginBottom:10,
    marginLeft: 20,
    fontWeight: "bold",
    fontSize: "3rem",

  },
  incrementIcon: {
    fontSize: 20,
    marginRight: 3,
    marginLeft: 5,
  },
  positiveIncrementIcon: {
    color: "green",
  },
  negativeIncrementIcon: {
    color: "red",
  },
}));
export default function ProjectsSection(props) {
  const { projects,history } = props;
  const classes = useStyles();
  return (
    <React.Fragment>
      <div className={classes.sectionContainer}>
        <Typography variant="h5" className={classes.title}>
          Projects Overviews
        </Typography>

        <Grid container spacing={10} className={classes.projectsContainer}>
          {
                projects.map((project)=>
                <Grid item md={4} key={project} >
            <Card className={classes.projectContainer} onClick={()=>history.push('/project')}>
              <CardContent >
                <Grid container justify="space-between">
                  <Grid item>
                    <Typography className={classes.projectTitle}>
                      {project.name}(Last 30 days)
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Camera color="secondary" />
                  </Grid>
                </Grid>
                <Typography className={classes.projectProgress}>
                  {project.progress} %
                </Typography>
                {project.increment > 0 ? (
                  <Grid container>
                    <Grid item>
                      <TrendingUpIcon
                        className={
                          classes.incrementIcon +
                          " " +
                          classes.positiveIncrementIcon
                        }
                      />
                    </Grid>
                    <Grid item>
                      <Typography
                        variant="caption"
                        className={classes.incrementText}
                      >
                        {project.increment} % less activities than usual
                      </Typography>
                    </Grid>
                  </Grid>
                ) : (
                  <Grid container>
                    <Grid item>
                      <TrendingDownIcon
                        className={
                          classes.incrementIcon +
                          " " +
                          classes.negativeIncrementIcon
                        }
                      />
                    </Grid>
                    <Grid item>
                      <Typography
                        variant="caption"
                        className={classes.incrementText}
                      >
                        {project.increment} % less activities than usual
                      </Typography>
                    </Grid>
                  </Grid>
                )}
              </CardContent>
            </Card>
          </Grid>
                )
            }
          
        </Grid>
      </div>
    </React.Fragment>
  );
}
