export default function ProviderResults(props) {
  const { providers } = props;

  const classes = useStyles();

  const RenderDragElement = (props) => {
    const { provider } = props;
    return (
      <Grid
        container
        spacing={2}
        alignItems="center"
        style={{ marginTop: 2, marginBottom: 2 }}
      >
        <Grid item>
          <DragIndicatorIcon style={{ fontSize: 50 }} />
        </Grid>
        <Grid item>
          <img
            src={provider.image}
            width={100}
            height={100}
            style={{ borderRadius: "50%" }}
          />
        </Grid>
        <Grid item md={7}>
          <Grid direction="column" container>
            <Typography variant="h5" color="primary">
              {provider.name}
            </Typography>
          </Grid>
          <Grid direction="column" container>
            <Typography>{provider.description}</Typography>
          </Grid>
        </Grid>
      </Grid>
    );
  };

  if (providers) {
    return (
      <Grid container justify="center" style={{marginTop:20}}>

        <Droppable droppableId="providerDroppable">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
            ><Typography variant="h5" color="primary" style={{ marginLeft: 20 }}>
            Provider Results
           </Typography>
              {providers.length > 0 ? (
                providers.map((provider, index) => (
                  <Draggable
                    key={provider.id}
                    draggableId={provider.id}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                         <RenderDragElement provider={provider} />
                      </div>
                    )}
                  </Draggable>
                ))
              ) : (
                <div style={{ height: 50 }}>No Providers</div>
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>

       
      </Grid>
    );
  } else {
    return <Typography variant="h3"> </Typography>;
  }
}
