import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const materialDetailsSlice = createSlice({
    name: 'materialDetails',
    initialState: {
        materialDetails : null
    },
    reducers: {
        loadMaterialDetailsSuccess: (state, action) => {
            state.materialDetails=action.payload
        }
    }
})

export default materialDetailsSlice.reducer;

const {loadMaterialDetailsSuccess} = materialDetailsSlice.actions;

export const loadMaterialDetails = (materialId) => async dispatch => {
    try {
        const res = await api.get('/providerMaterials?id='+materialId);
        console.log("materials Details",res.data)
        dispatch(loadMaterialDetailsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const editMaterialDetails = (materialId,values,callback) => async dispatch => {
    try {
        const res = await api.patch('/providerMaterials/'+materialId,values );
        console.log("materials Details",res.data)
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}