import { Divider, List, ListItem, Typography } from '@material-ui/core';
import { FiberManualRecord } from '@material-ui/icons';
// import Rating from '@material-ui/lab/Rating';
import { createStyles, makeStyles } from '@material-ui/styles';
import theme from 'assets/jss/theme';
import React from 'react'
import { Link } from 'react-router-dom';


const useStyles = makeStyles((theme)=>
createStyles({
      // descritpiton area
      descriptionContainer: {
        paddingLeft: "20px",
        paddingRight: "20px",
      },
      materialName: {
        fontWeight: "bold",
        marginBottom: 10,
      },
      featuredTexts: {
        color: theme.palette.primary.main,
        fontSize: "0.9em",
      },
      priceText: {
        color: theme.palette.primary.main,
      },
      sectionContainer: {
        marginTop: 10,
        marginBottom: 10,
      },
}))
export default function DescriptionArea(props) {
  const classes= useStyles();
    const { material } = props;
    return (
      <React.Fragment>
        <div className={classes.descriptionContainer}>
          <Typography variant="h5" className={classes.materialName}>
            {material.name}
          </Typography>
  
          <Typography
            className={classes.featuredTexts}
            component={Link}
            to={`/providers/${material.provider_id}`}
          >
            Visit Provider Store
          </Typography>
  
          <Typography className={classes.featuredTexts}>
            {/* <Rating
              name="read-only"
              value={material.rating}
              readOnly
              size="small"
              style={{ marginRight: 10 }}
            /> */}
            {material.number_of_ratings} {" ratings | "}{" "}
            {material.answered_question_no} {" answered questions"}
          </Typography>
          <Divider />
          <div className={classes.sectionContainer}>
            <Typography
              variant="body2"
              className={classes.priceText}
              component="p"
            >
              Price: {material.price}ETB
            </Typography>
            <Typography
              variant="body2"
              className={classes.priceText}
              component="p"
            >
              Projected Price: {material.price * 1.2}ETB
            </Typography>
            <Typography variant="body2" component="p">
              Location: {material.location}
            </Typography>
          </div>
  
          <Typography variant="body2" color="textSecondary" component="p">
            {material.deliverable}
          </Typography>
          <Divider />
          <div style={{ marginTop: 10 }} />
          <Typography style={{ color:theme.palette.primary.main }} component="p">
            Description:
          </Typography>
          <Typography component="p" style={{ display: "inline-block" }}>
            {material.description}
          </Typography>
          <List>
            {material.aboutItem.map((about) => (
              <ListItem>
                <Typography variant="body2" color="textSecondary">
                  <FiberManualRecord
                    style={{ color: theme.palette.primary.main, fontSize: "10" }}
                  />{" "}
                  {about}
                </Typography>
              </ListItem>
            ))}
          </List>
        </div>
      </React.Fragment>
    );
}
