import {
  Button,
  Card,
  Checkbox,
  Grid,
  IconButton,
  TextField,
  Typography,
} from "@material-ui/core";
import { Add, Check, Close } from "@material-ui/icons";
import theme from "assets/jss/theme";
import React, { useState } from "react";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";

const initialLists = [
  
];

const RenderListItem = (props) => {
  const { listItem } = props;
  const [status, setStatus] = useState(listItem.value);

  const handleChange = (event) => {
    setStatus(!status);
  };

  return (
    <React.Fragment>
      <Grid container alignItems="center" alignContent="center">
        <Grid item>
          <FormControl component="fieldset" style={{ margin: 0 }}>
            <FormGroup style={{ margin: 0 }}>
              <FormControlLabel
                style={{ margin: 0 }}
                control={
                  <Checkbox
                    checked={status}
                    onChange={handleChange}
                    color="primary"
                  />
                }
              />
            </FormGroup>
          </FormControl>
        </Grid>
        <Grid item>
          {status ? (
            <Typography
              variant="p"
              style={{
                textDecoration: "line-through",
                color: theme.palette.primary.light,
              }}
            >
              {listItem.name}
            </Typography>
          ) : (
            <Typography variant="p">{listItem.name}</Typography>
          )}
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default function ChecklistSection() {
  const [lists, setLists] = useState(initialLists);
  const [showTextField, setShowTextField] = useState(false);
  const [newList, setNewList] = useState("");

  const handleChange = (event) => {
    setNewList(event.target.value);
  };

  const handleAddList = () => {
    setLists([...lists, { name: newList, value: false }]);
    setShowTextField(false);
    setNewList(null);
  };

  const handlePlus = () => {
    setShowTextField(true);
  };
  const handleClose = () => {
    setShowTextField(false);
  };

  const CheckListHeader = () => {
    return (
      <Grid container justify="space-between" style={{marginLeft:20, paddingRight:20, paddingTop:20}}>
        <Grid item>
          <Typography style={{ fontWeight: "bold" }}>Checklist</Typography>
        </Grid>
        <Grid item>
           <Grid container >
        <Grid
          item
          style={{
            backgroundColor: theme.palette.primary.light,
            color: "white",
            padding: "10px",
            borderRadius: 10,
            fontSize: 10,
            marginLeft: 10,
            marginRight: 10,
          }}
        >
          percent
        </Grid>
        <Grid
          item
          style={{
            backgroundColor: theme.palette.primary.main,
            color: "white",
            padding: "10px",
            borderRadius: 10,
            fontSize: 10,
            marginLeft: 10,
            marginRight: 10,
          }}
        >
          percent
        </Grid>
        </Grid>
        </Grid>
       
       
      </Grid>
    );
  };

  const CheckLists = () => {
    return (
      <Grid container direction="column">
        {lists.map((list, index) => (
          <RenderListItem listItem={list} />
        ))}

        <Grid item>
          <Button
            color="primary"
            startIcon={<Add />}
            onClick={handlePlus}
            variant="outlined"
            style={{ marginTop: 20 }}
          >
            Add item
          </Button>

          {showTextField ? (
            <Grid item>
              <Grid container justify="center" alignItems="center">
                <Grid item>
                  <form>
                    <TextField
                      variant="standard"
                      label="New CheckList"
                      value={newList}
                      onChange={handleChange}
                    />
                  </form>
                </Grid>
                <Grid item>
                  <IconButton>
                    <Check
                      style={{ color: "green", padding: 5 }}
                      onClick={handleAddList}
                    />
                  </IconButton>
                </Grid>
                <Grid item>
                  <IconButton>
                    <Close
                      style={{ color: "red", padding: 5 }}
                      onClick={handleClose}
                    />
                  </IconButton>
                </Grid>
              </Grid>
            </Grid>
          ) : null}
        </Grid>
      </Grid>
    );
  };

  return (
    <Card
      style={{ width: "80%", padding: 10, paddingLeft: 20, paddingBottom: 20 }}
    >
      <Grid container direction="column">
        <Grid item>
          <CheckListHeader />
        </Grid>
        <Grid item>
          <CheckLists />
        </Grid>
      </Grid>
    </Card>
  );
}
