import React, { useState } from "react";

import studio1 from "assets/img/backgrounds/landing-bg.jpg";
import { useDispatch, useSelector } from "react-redux";
import {
  Dialog,
  IconButton,
  makeStyles,
  useTheme,
  Grid,
  Button,
  Slider,
} from "@material-ui/core";

import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { Edit } from "@material-ui/icons";
import { Field, reduxForm } from "redux-form";
import { renderTextField } from "components/ReduxInput/ReduxInput";

import {
  loadContractorDesigns,
  editContractorDesign,
} from "features/contractors/contractorDesignsSlice";
import { container } from "assets/jss/commons";

import profile1 from "assets/img/faces/christian.jpg";
import profile2 from "assets/img/faces/avatar.jpg";
import profile4 from "assets/img/faces/card-profile1-square.jpg";
import profile5 from "assets/img/faces/card-profile2-square.jpg";
import profile7 from "assets/img/faces/card-profile4-square.jpg";
import profile8 from "assets/img/faces/card-profile6-square.jpg";
import profile10 from "assets/img/faces/kendall.jpg";
import profile11 from "assets/img/faces/marc.jpg";
import profile12 from "assets/img/faces/wideImage.jpg";
import { renderRadioButton } from "components/ReduxInput/ReduxInput";

const imageList = [
  profile1,
  profile2,
  profile4,
  profile5,
  profile12,
  profile7,
  profile8,
  profile10,
  profile11,
];

const useStyles = makeStyles((theme) => ({
  container: {
    ...container,
  },
  root: {
    display: "flex",
    marginTop: "20px",
    marginBottom: "40px",
    marginRight: "100px",

    backgroundColor: theme.palette.grey[900],
  },
  details: {
    display: "flex",
    flexDirection: "column",
  },
  content: {
    flex: "1 0 auto",
  },
  cover: {
    width: "30%",
    height: "auto",
    minHeight: 300,
    minWidth: 300,
  },
  controls: {
    display: "flex",
    alignItems: "center",
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  editButton: {
    color: "white",
  },
  editModal: {
    width: "55%",
    padding: 50,
  },

  textField: {
    margin: 20,
    marginTop: "20px",
    width: "80%",
  },
}));

const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let EditDesignForm = reduxForm({
  form: "editDesign",
});

EditDesignForm = (props) => {
  const {
    handleSubmit,
    pristine,
    reset,
    submitting,
    classes,
    budgetValue,
    handleBudgetChange,
  } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Grid container justify="center">
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="title"
              component={renderTextField}
              label="Project Title"
              className={classes.textField}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="preference"
              component={renderRadioButton}
              label="Project Preference"
              className={classes.textField}
              radioValues={[
                "remodeling",
                "interior",
                "landscape",
                "residential",
                "commercial",
                "public",
              ]}
            />
          </Grid>

          <Grid container justify="center">
            <Grid item={0}>{budgetValue[0] * 50000}ETB - </Grid>
            <Grid item={0}>{budgetValue[1] * 50000}ETB</Grid>
          </Grid>

          <Slider value={budgetValue} onChange={handleBudgetChange} />

          <Grid container justify="space-between">
            <Grid item={0}>0</Grid>
            <Grid item={0}>5,000,000</Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="completion"
              component={renderRadioButton}
              label="Project Completion"
              className={classes.textField}
              radioValues={[
                "Completed",
                "Under_construction",
                "Not_started",
                "Canceled",
              ]}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About the Design"
              multiline={true}
              variant="outlined"
            />
          </Grid>

          <Button type="submit" color="primary" size="large">
            Update Design
          </Button>
        </Grid>
      </form>
    </div>
  );
};

EditDesignForm = reduxForm({
  form: "createProject",
})(EditDesignForm);

const ContractorDesignsSection = (props) => {
  const { ...rest } = props;

  const classes = useStyles();

  const dispatch = useDispatch();

  const { contractorDesigns } = useSelector((state) => state.contractorDesigns);
  const { user } = useSelector((state) => state.user);

  const [selectedDesign, setSelectedDesign] = useState(null);
  const [open, setOpen] = useState(false);
  const [openEditDesign, setOpenEditDesign] = useState(false);

  const [budgetValue, setBudgetValue] = useState([0, 100]);
  const handleBudgetChange = (event, newValue) => {
    setBudgetValue(newValue);
    console.log("budget Value", budgetValue);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = (value) => {
    setOpen(false);
  };

  const handleOpenEditModal = () => {
    setOpenEditDesign(true);
  };
  const handleCloseEditModal = (value) => {
    setOpenEditDesign(false);
  };

  const EditSuccessful = () => {
    dispatch(loadContractorDesigns(user.id));
    setOpenEditDesign(false);
  };

  const submit = (values) => {
    const val={...values, budgetValue:budgetValue}
    dispatch(
      //  (values,user.id, ()=>setDesignModal(false))
      editContractorDesign(selectedDesign, val, () => EditSuccessful())
    );
  };

  return (
    <React.Fragment>
      <div>
        {contractorDesigns !== null ? (
          contractorDesigns.map((design, index) => (
            <React.Fragment key={design.id}>
              <Dialog
                onClose={handleClose}
                aria-labelledby="simple-dialog-title"
                open={open}
              >
                <img src={imageList[index % 8]} />
              </Dialog>

              <Dialog
                onClose={handleCloseEditModal}
                aria-labelledby="simple-dialog-title"
                open={openEditDesign}
                maxWidth="lg"
                classes={{ paper: classes.editModal }}
              >
                <EditDesignForm
                  onSubmit={submit}
                  classes={classes}
                  budgetValue={budgetValue}
                  handleBudgetChange={handleBudgetChange}
                />
              </Dialog>
              <div className={classes.container}>
                <Card className={classes.root}>
                  <CardMedia
                    onClick={handleClickOpen}
                    className={classes.cover}
                    image={imageList[index % 9]}
                    title="Live from space album cover"
                  />
                  <div className={classes.details}>
                    <CardContent className={classes.content}>
                      <Grid container direction="row" justify="space-between">
                        <Grid item>
                          <Typography
                            color="primary"
                            component="h5"
                            variant="h5"
                          >
                            {design.title}
                          </Typography>
                        </Grid>
                        {user.id === design.contractor_id ? (
                          <Grid item>
                            <IconButton
                              className={classes.editButton}
                              onClick={handleOpenEditModal}
                              onClick={() => {
                                setSelectedDesign(design.id);
                                handleOpenEditModal();
                              }}
                            >
                              <Edit />
                            </IconButton>
                          </Grid>
                        ) : null}
                      </Grid>

                      <Typography variant="body1" style={{ color: "white" }}>
                        Design Preference: {design.preference}
                      </Typography>
                      <Typography variant="body1" style={{ color: "white" }}>
                      Budget: {design.budgetValue[0]*50000}ETB -  {design.budgetValue[1]*50000}ETB
                      </Typography>
                      
                      <Typography variant="body1" style={{ color: "white" }}>
                        Project Status: {design.completion}
                      </Typography>
                      <Typography variant="body1" style={{ color: "white" }}>
                        Description: {design.description}
                      </Typography>
                    </CardContent>
                  </div>
                </Card>
              </div>
            </React.Fragment>
          ))
        ) : (
          <div>No Designs Found</div>
        )}
      </div>
    </React.Fragment>
  );
};
export default ContractorDesignsSection;
