import { fade, Grid, Typography } from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/styles";
import React from "react";

import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import { container } from "assets/jss/commons";

import materialImage from "assets/img/materials/material1.jpg";
import { useSelector } from "react-redux";

import material1 from "assets/img/materials/material1.jpg";
import material2 from "assets/img/materials/material2.jpg";
import material3 from "assets/img/materials/material3.jpg";
import material4 from "assets/img/materials/material4.jpg";
import material5 from "assets/img/materials/material5.jpg";
import { getListStyle } from "components/DNDElement/DNDElement";
import { getItemStyle } from "components/DNDElement/DNDElement";


import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

const useStyles = makeStyles((theme) =>
  createStyles({
    container: {
      // ...container,
      zIndex: "2",
      color: "#FFFFFF",
      position: "relative",
      marginTop: 0,
      paddingTop: "40px",
    },
    root: {
      maxWidth: 345,
      height: 340,
      backgroundColor: fade("#FFF", 0.9),
    },
    media: {
      width:'100%',
      height: 140,
    },
    rootSelected: {
      height: 345,
      backgroundColor: fade("#FFF", 0.15),
    },
    cardFooter: {
      height: 200,
      overflow: "auto",
    },
    cardContainer: {
      padding: 10,
    },
  })
);

export default function MaterialResults(props) {
  const classes1 = useStyles();

  const { history } = props;

  const materialsImage = [
    material1,
    material2,
    material3,
    material4,
    material5,
  ];

  // const {providerMaterials} = useSelector(state => state.providerMaterials)

  const { providerMaterials } = props;

  return (
    <div className={classes1.container}>
      <Grid container justify="center">
        <Grid item>
        <Droppable droppableId="materialDroppable">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
            >
            <Grid container justify="flex-start">
              {providerMaterials !== null ? (
                providerMaterials.length > 0 ? (
                  providerMaterials.map((material, index) => (
                     <Grid
                            key={material.id}
                            item
                            md={4}
                            className={classes1.cardContainer}
                          > 
                    <Draggable
                      key={material.id}
                      draggableId={material.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                         
                        >
                         
                          <div
                          className={classes1.root}
                              key={material.id}>
                                <img src={materialsImage[index % 5]}
                                className={classes1.media} />
                                <div>
                                <CardContent className={classes1.cardFooter}>
                                  <Typography
                                  color='primary'
                                    variant="h5"
                                    component="h2"
                                  >
                                    {material.name}
                                  </Typography>

                                  <Typography
                                    variant="body2"
                                    color="primary"
                                    component="p"
                                   
                                  >
                                    Price: {material.price}ETB
                                  </Typography>
                                  <Typography variant="body2" component="p"  style={{color:'black'}}>
                                    {material.location}
                                  </Typography>
                                  <Typography
                                    variant="body2"
                                    color="textSecondary"
                                    component="p"
                                    style={{color:'black'}}
                                  >
                                    {material.deliverable}
                                  </Typography>
                                </CardContent>
                                </div>
                          </div>
                         
                        </div>
                      )}
                    </Draggable>
                    </Grid>
                  ))
                ) : null
              ) : (
                <div style={{ height: 50 }}>No Architects</div>
              )}
              {provided.placeholder}
            </Grid>
            </div>
          )}
        </Droppable>
        </Grid>
      </Grid>
    </div>
  );
}
