import {
  Card,
  Grid,
  makeStyles,
  Typography,
} from "@material-ui/core";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import React from "react";
import { container } from "assets/jss/commons";
import { itemPrices } from "assets/data";
import ListComponent from "./components/ListComponent";
import Parallax from "components/Parallax/Parallax";
import classNames from "classnames";
import TableHeader from "./components/TableHeader";
import InsertData from "./components/InsertData";

import backgroundImage from "assets/img/backgrounds/bg.jpg";

const useStyles = makeStyles((theme) => ({
  container: {
    zIndex: "12",
    color: "#FFFFFF",
    paddingTop: "100px",
    marginLeft: "1rem",
    ...container,
  },
  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",
  },
  mainRaised: {
    margin: "-60px 30px 60px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  },
  itemContainer: {
    padding: 10,
    backgroundColor: "transparent",
    paddingTop:20
  },
  headerContainer: {
    width: "100%",
    flexGrow: 1,
  },
  headerItem: {
    textAlign: "center",
  },
  headerText: {},
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

export default function MarketPrices(props) {
  const { ...rest } = props;

  const classes = useStyles();

  return (
    <React.Fragment>
      <Header
        color="transparent"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
        {...rest}
      />
      <Parallax small filter image={backgroundImage} />
      <div className={classNames(classes.main, classes.mainRaised)}>
        <Card className={classes.itemContainer}>
          <Grid container justify='space-between' style={{marginBottom:20}}>
<Grid item>
    <Typography variant='h3' style={{marginLeft:40}}>
            Market Prices
          </Typography>
</Grid>
<Grid item>
    <InsertData/>
</Grid>
          </Grid>
        
      
          <Grid
            container
            justify="space-around"
            className={classes.headerContainer}
          >
            
            <TableHeader />

            {itemPrices.map((item, index) => (
              <>
                <ListComponent item={item} index={index} />
              </>
            ))}
          </Grid>
        </Card>
      </div>
    </React.Fragment>
  );
}
