import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const providerMaterialsSlice = createSlice({
    name: 'providerMaterials',
    initialState: {
        providerMaterials : null
    },
    reducers: {
        loadProviderMaterialsSuccess: (state, action) => {
            state.providerMaterials=action.payload
        }
    }
})

export default providerMaterialsSlice.reducer;

const {loadProviderMaterialsSuccess} = providerMaterialsSlice.actions;

export const loadProviderMaterials = (providerId) => async dispatch => {
    try {
        const res = await api.get('/providerMaterials?provider_id='+providerId);
        console.log("providers Materials",res.data)
        dispatch(loadProviderMaterialsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}
export const loadProviderMaterialsAll = () => async dispatch => {
    try {
        const res = await api.get('/providerMaterials');
        console.log("providers Materials",res.data)
        dispatch(loadProviderMaterialsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}
export const addProviderMaterial = (values, providerId,userId,callback) => async dispatch => {
    const val= {...values,
        provider_id:providerId,
        user_id:userId
    }
    try {
       await api.post('/providerMaterials',val);
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}

export const editProviderMaterial = (materialId,values,callback) => async dispatch => {
    
    try {
        await api.patch('/providerMaterials/'+materialId,values );
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}