import { Button, Grid, Typography,Card } from "@material-ui/core";

import { makeStyles } from "@material-ui/styles";
import React from "react";
import Box from "@material-ui/core/Box";
import LinearProgress from "@material-ui/core/LinearProgress";

import PropTypes from "prop-types";

import CircularProgress from "@material-ui/core/CircularProgress";



const useStyles = makeStyles((theme) => ({
    tasksSectionContainer: {
      backgroundColor: "white",
      width: "100%",
      padding: 20,
    },
    percentage: {
      backgroundColor: theme.palette.primary.main,
      color: "white",
      padding: "10px",
      borderRadius: 10,
      fontSize: 10,
      marginLeft: 10,
      marginRight: 10,
    },
    taskListContainer: {
      padding: 20,
    },
    taskContainer: {},
    circleIcon: {
      fontSize: 25,
      //   fontWeight:10
    },
    root: {
      width: "100%",
      marginBottom: 10,
    },
    contribute:{
        backgroundColor:theme.palette.primary.light,
        color:'white',
        paddingRight:20,
        paddingLeft:20,
        borderRadius:10
    },
    netsaDesignText: {
        ...theme.typography.estimate,
        borderRadius: "50px",
        marginLeft: "50px",
        marginRight: "50px",
        height: "45px",
        color:theme.palette.secondary.dark,
        fontSize:'35px',
        textTransform:'uppercase'
      },
  }));

 export default function NetsaDesignSection() {
    
    const classes= useStyles();
  
    const FinancialHeader = () => {
      return (
        <Grid container justify="space-between" direction='column' spacing={2}>
          <Grid item>
            <Typography className={classes.netsaDesignText}>Netsa</Typography>
          </Grid>
          <Grid item>
            <Typography className={classes.netsaDesignText}>Design</Typography>
          </Grid>
        </Grid>
      );
    };
  
    function CircularProgressWithLabel(props) {
      return (
        <Box position="relative" display="inline-flex">
          <CircularProgress variant="determinate" {...props} size={100} />
          <Box
            top={0}
            left={0}
            bottom={0}
            right={0}
            position="absolute"
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            <Typography
              variant="caption"
              component="div"
              color="textSecondary"
            >{`${Math.round(props.value)}%`}</Typography>
          </Box>
        </Box>
      );
    }
  
    CircularProgressWithLabel.propTypes = {
      /**
       * The value of the progress indicator for the determinate variant.
       * Value between 0 and 100.
       */
      value: PropTypes.number.isRequired,
    };
  
    const CircularProgressSection = () => {
      return (
        <Grid container direction="column">
          <Grid item>
            {/* <CircularProgress value={75} variant="determinate"/> */}
            <CircularProgressWithLabel value={74} />
  
           
          </Grid>
          <Grid item>
          <Typography variant='caption'>Target: 50,000ETB</Typography>
          </Grid>
          <Grid item>
          <Typography variant='caption'>Reached: {50000*0.74}ETB</Typography>
          </Grid>
        </Grid>
      );
    };
  
    const HorizontalProgresses = () => {
      const progress = [45, 5, 78];
  
      return (
        <Grid container direction="column">
          <Grid item style={{ width: "200px", marginBottom: 10 }}>
              <Grid container direction='column'>
                  <Grid item>
                      <Grid container style={{marginBottom:10, marginTop:10}} justify='center'>
                          <Grid item >
                             <Typography style={{color:'grey', fontSize:13}}>Project One:{' '}</Typography> 
                          </Grid>
                          <Grid item >
                          <Typography style={{color:'green', fontSize:13, fontWeight:'bold'}}>50%</Typography> 
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item>
                      <LinearProgress variant="determinate" value={progress[0]} />
                  </Grid>
              </Grid>
          
          </Grid>
          <Grid item style={{ width: "200px", marginBottom: 10 }}>
              <Grid container direction='column'>
                  <Grid item>
                      <Grid container style={{marginBottom:10 ,marginTop:10}} justify='center'>
                          <Grid item >
                             <Typography style={{color:'grey', fontSize:13}}>Project Two:{' '}</Typography> 
                          </Grid>
                          <Grid item >
                          <Typography color='secondary' style={{ fontSize:13, fontWeight:'bold'}}>5%</Typography> 
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item>
                      <LinearProgress variant="determinate" value={progress[1]} />
                  </Grid>
              </Grid>
          
          </Grid>
          <Grid item style={{ width: "200px", marginBottom: 10 }}>
              <Grid container direction='column'>
                  <Grid item>
                      <Grid container style={{marginBottom:10, marginTop:10}} justify='center'>
                          <Grid item >
                             <Typography style={{color:'grey', fontSize:13}}>Project Three:{' '}</Typography> 
                          </Grid>
                          <Grid item >
                          <Typography style={{color:'green', fontSize:13, fontWeight:'bold'}}>45%</Typography> 
                          </Grid>
                      </Grid>
                  </Grid>
                  <Grid item>
                      <LinearProgress variant="determinate" value={progress[2]} />
                  </Grid>
              </Grid>
          
          </Grid>
        </Grid>
      );
    };
    const Contribute = () => {
      return <Button className={classes.contribute}>Contribute</Button>;
    };
  
    return (
        <Card style={{margin:20, marginTop:'20%', padding:20, backgroundColor:'#c4e4c5'}} >
            <Grid
        container
        direction="column"
        spacing={3}
        justify="center"
        alignItems="center"
        alignContent="center"
      >
        <Grid item>
          <FinancialHeader />
        </Grid>
        <Grid item>
          <CircularProgressSection />
        </Grid>
        <Grid item>
          <HorizontalProgresses />
        </Grid>
        <Grid item>
          <Contribute />
        </Grid>
      </Grid> 
        </Card>
     
    );
  };