import { loadProviders } from 'features/providers/providersSlice';
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CreateProviderProfile from '../CreateProviderProfile/CreateProviderProfile';
import MyProfile from './MyProfile';

export default function MyProviderPage(props) {

    const {providers} = useSelector(state => state.providers);
    
    const user = useSelector(state => state.user)

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(
            loadProviders()
        ) 
        
    }, [])

    return (
      
        providers !==null 
        
        ?
        
        providers.some((provider)=> provider.user_id === user.user.id) ? 
        <MyProfile providerId={providers.filter((provider)=>provider.user_id===user.user.id)[0].id} /> 
        : 
       <CreateProviderProfile userId={user.user.id} history={props.history} />
        :
        <div>
            Loading
        </div>
    )
}
