import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  makeStyles,
  Slide,
  Typography,
} from "@material-ui/core";

import { Close, Create } from "@material-ui/icons";

import { title } from "assets/jss/commons";

import imagesStyle from "assets/jss/imagesStyles";
import { Field, reduxForm } from "redux-form";

import {
  renderTextField,
  renderSelectField,
} from "components/ReduxInput/ReduxInput";
import { countryList } from "assets/data";
import { editProviderDetails } from "features/providers/providerDetailsSlice";
import { useDispatch } from "react-redux";
import { loadProviderDetails } from "features/providers/providerDetailsSlice";

import ImageUploader from "components/ImageUpload/ImageUploader";

const useStyles = makeStyles({
  profile: {
    textAlign: "center",
    "& img": {
      maxWidth: "160px",
      width: "100%",
      margin: "0 auto",
      transform: "translate3d(0, -50%, 0)",
    },
  },
  description: {
    margin: "1.071rem auto 0",
    maxWidth: "600px",
    color: "#999",
    textAlign: "center !important",
  },
  name: {
    marginTop: "-80px",
  },
  ...imagesStyle,
  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",
  },
  mainRaised: {
    margin: "-60px 30px 20px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  },
  title: {
    ...title,
    display: "inline-block",
    position: "relative",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none",
    marginLeft: "20px",
  },
  titleSecondary: {
    ...title,
    display: "inline-block",
    position: "relative",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none",
    marginLeft: "20px",
  },
  formContainer: {
    marginLeft: "50px",
  },
  textField: {
    margin: 20,
    marginTop: "20px",
    width: "100%",
  },
  selectField: {
    margin: 20,
    marginTop: "20px",

    width: 300,
  },
});

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let ProviderEditForm = reduxForm({
  form: "providerEdit",
});

ProviderEditForm = (props) => {
  const { handleSubmit, classes, providerType } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <div className={classes.formContainer}>
          <Grid container>
            <Grid item md={8}>
              {/* form */}
              <Grid container spacing={5} alignItems="center">
            <Grid item md={5}>
              <Field
                name="name"
                component={renderTextField}
                label="Name"
                className={classes.textField}
              />
            </Grid>

            {providerType === "company" ? (
              <React.Fragment>
                <Grid item md={5}>
                  <Field
                    name="tin_number"
                    component={renderTextField}
                    label="Tin Number"
                    className={classes.textField}
                  />
                </Grid>
              </React.Fragment>
            ) : null}

            {providerType === "individual" ? (
              <React.Fragment>
                <Grid item md={5}>
                  <Field
                    name="age"
                    component={renderTextField}
                    label="Age"
                    type="number"
                    className={classes.textField}
                  />
                </Grid>
                <Grid item md={5}>
                  <Field
                    name="gender"
                    component={renderSelectField}
                    className={classes.selectField}
                    label="Gender"
                  >
                    <option value="" />
                    <option value={"Male"}>Male</option>
                    <option value={"Female"}>Female</option>
                  </Field>
                </Grid>
                <Grid item md={5}>
                  <Field
                    name="nationality"
                    component={renderSelectField}
                    className={classes.selectField}
                    label="Nationality"
                  >
                    <option value="" />

                    {countryList.map((country) => (
                      <option key={country} value={country}>
                        {country}
                      </option>
                    ))}
                  </Field>
                </Grid>
              </React.Fragment>
            ) : null}

            <Grid item md={5}>
              <Field
                name="location"
                component={renderTextField}
                label="Location"
                type="location"
                className={classes.textField}
              />
            </Grid>
            <Grid item md={5}>
              <Field
                name="website"
                component={renderTextField}
                label="Website"
                type="url"
                className={classes.textField}
              />
            </Grid>
          </Grid>
            </Grid>

            {/* image uploader */}
            <Grid
              item
              md={3}
              justify="center"
              alignContent="center"
              alignItems="center"
            >
              <ImageUploader />
            </Grid>
          </Grid>

         

          <Grid container spacing={2} alignItems="center">
            <Grid item md={3}>
              <Field
                name="fb_account"
                component={renderTextField}
                className={classes.textField}
                label="Facebook account link"
              />
            </Grid>
            <Grid item md={3}>
              <Field
                name="twitter_account"
                component={renderTextField}
                className={classes.textField}
                label="Twitter account link"
              />
            </Grid>
            <Grid item md={3}>
              <Field
                name="instagram_account"
                component={renderTextField}
                className={classes.textField}
                label="Instagram account link"
              />
            </Grid>
          </Grid>

          <Grid container spacing={5} alignItems="center">
            <Grid item md={11}>
              <Field
                name="short_description"
                component={renderTextField}
                className={classes.textField}
                label="Short Description About Self"
              />
            </Grid>
          </Grid>

          <Button type="submit" color="primary" size="large">
            Submit
          </Button>
        </div>
      </form>
    </div>
  );
};

ProviderEditForm = reduxForm({
  form: "createProject",
})(ProviderEditForm);

export default function ProfileEditModal(props) {
  const classes = useStyles();
  const { providerId, providerType } = props;

  const [profileModal, setProfileModal] = useState(false);

  const editedCallback = () => {
    setProfileModal(false);
    dispatch(loadProviderDetails(providerId));
  };

  const dispatch = useDispatch();
  const submit = (values) => {
    dispatch(editProviderDetails(providerId, values, () => editedCallback()));
    console.log(values);
  };

  return (
    <React.Fragment>
      <Grid
        container
        direction="row"
        justify="flex-end"
        alignItems="center"
        className={classes.editButtonContainer}
      >
        <Button color="primary" onClick={() => setProfileModal(true)}>
          <Create color="primary" fontSize="large" />
        </Button>
      </Grid>
      <Dialog
        classes={{
          root: classes.center,
          paper: classes.modal,
        }}
        maxWidth="lg"
        open={profileModal}
        TransitionComponent={Transition}
        keepMounted
        onClose={() => setProfileModal(false)}
        aria-labelledby="classic-modal-slide-title"
        aria-describedby="classic-modal-slide-description"
      >
        <DialogTitle
          id="classic-modal-slide-title"
          disableTypography
          className={classes.modalHeader}
        >
          <Grid container justify="space-between">
            <Grid item>
              <h4 className={classes.modalTitle}>Edit Your Profile</h4>
            </Grid>
            <Grid item>
              <IconButton
                className={classes.modalCloseButton}
                key="close"
                aria-label="Close"
                color="inherit"
                onClick={() => setProfileModal(false)}
              >
                <Close className={classes.modalClose} />
              </IconButton>
            </Grid>
          </Grid>
        </DialogTitle>
        <DialogContent
          id="classic-modal-slide-description"
          className={classes.modalBody}
        >
          <ProviderEditForm
            onSubmit={submit}
            classes={classes}
            providerType={providerType}
          />
        </DialogContent>
      </Dialog>
    </React.Fragment>
  );
}
