import React, { useEffect } from "react";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import Parallax from "components/Parallax/Parallax";

import classNames from "classnames";
import {
  makeStyles,
  Slide,
} from "@material-ui/core/";

import { useDispatch } from "react-redux";

import backgroundImage from "assets/img/backgrounds/landing-bg.jpg";

import { loadProviderDetails } from "features/providers/providerDetailsSlice";
import { loadProviderMaterials } from "features/providers/providerMaterialsSlice";
import ProviderDescriptionSection from '../ProviderProfilePage/sections/ProviderDescriptionSection';
import ProviderMaterialsSection from '../ProviderProfilePage/sections/ProviderMaterialsSection';
import AddMaterialModal from '../ProviderProfilePage/sections/AddMaterialModal';


const useStyles = makeStyles((theme) => ({
  container: {
    paddingRight: "0",
    paddingLeft: "0",

    paddingTop: "0px",
    marginRight: "auto",
    marginLeft: "auto",
    // width: "100%",
  },

  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",
  },
  mainRaised: {
    margin: "-60px 30px 0px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

Transition.displayName = "Transition";

export default function MyProfile(props) {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadProviderDetails(props.providerId));
    dispatch(loadProviderMaterials(props.providerId));
  }, [props.providerId,dispatch]);

  return (
    <React.Fragment>
      <Header
        color="transparent"
        brand="Nidf"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white",
        }}
      />
      <Parallax small filter image={backgroundImage} />

      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <ProviderDescriptionSection />
          <AddMaterialModal />
          <ProviderMaterialsSection />
        </div>
      </div>
    </React.Fragment>
  );
}
