import {
    Button,
    fade,
    Grid,
    Typography,
  } from "@material-ui/core";
  import { makeStyles } from "@material-ui/styles";
  import React, {  useState } from "react";
  import { Link } from "react-router-dom";
  import Checkbox from "@material-ui/core/Checkbox";
  
  import { container } from "assets/jss/commons";
  
  import image from "assets/img/blurred/blurred1.png";
  import { channels } from "assets/data";
  
  import Card from "@material-ui/core/Card";
  import CardActionArea from "@material-ui/core/CardActionArea";
  import CardContent from "@material-ui/core/CardContent";
  import CardMedia from "@material-ui/core/CardMedia";
  
  const useStyles = makeStyles((theme) => ({
    container: {
      ...container,
      zIndex: "2",
      color: "#FFFFFF",
      position: "relative",
      marginTop: 0,
      paddingTop: "40px",
    },
    pageHeader: {
      height: "auto",
      display: "inherit",
      position: "relative",
      margin: "0",
      padding: "0",
      border: "0",
      alignItems: "center",
      "&:before": {
        //   background: "rgba(0, 0, 0, 0.5)"
      },
      "&:before,&:after": {
        position: "absolute",
        zIndex: "1",
        width: "100%",
        height: "100%",
        display: "block",
        left: "0",
        top: "0",
        content: '""',
      },
      "& footer li a,& footer li a:hover,& footer li a:active": {
        color: "#FFFFFF",
      },
      "& footer": {
        position: "absolute",
        bottom: "0",
        width: "100%",
      },
  
      // backgroundColor:fade('#FFF',0.35)
    },
    root: {
      maxWidth: 345,
      height: 340,
      backgroundColor: fade("#FFF", 0.9),
    },
    media: {
      height: 140,
    },
    rootSelected: {
      height: 345,
      backgroundColor: fade("#FFF", 0.15),
    },
    cardFooter: {
      height: 200,
      overflow: "auto",
    },
    cardContainer: {
      padding: 10,
    },
    buttonImport: {
      borderColor: theme.palette.common.orange,
      color: theme.palette.common.orange,
      borderWidth: 2,
      textTransform: "none",
      borderRadius: 50,
      fontFamily: "Roboto",
      fontWeight: "bold",
      fontSize: "0.9rem",
      height: 45,
      width: 120,
      margin:15,
      justifyContent:'flex-end',
      position:'fixed',
      right:10,
      bottom:5
    },
  }));
  
  const ItemsList = () => {
    const classes = useStyles();
  
    return (
      <React.Fragment>
        <div
          // style={{
          //   backgroundImage: "url(" + image + ")",
          //   backgroundSize: "cover",
          //   backgroundPosition: "top center",
          // }}
          // className={classes.pageHeader}
        >
          <div className={classes.container}>
           
            <div
             
            >
              <Grid container justify="flex-start">
                {channels.map((channel) => (
                  <Grid key={channel.id} item md={3} className={classes.cardContainer}>
                    <Card
                      className={classes.root }
                    >
                      <CardActionArea>
                        <CardMedia
                          className={classes.media }
                          image={channel.image}
                          title={channel.name}
                        >
                        </CardMedia>
  
                        <CardContent className={classes.cardFooter}>
                          <Typography gutterBottom variant="h5" component="h2">
                            {channel.name}
                          </Typography>
                          <Typography
                            variant="body2"
                            color="textSecondary"
                            component="p"
                          >
                            {channel.description}
                          </Typography>
                        </CardContent>
                      </CardActionArea>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  };
  
  export default ItemsList;
  