import { CardMedia, Grid } from '@material-ui/core';
import { createStyles, makeStyles } from '@material-ui/styles';
import React, { useState } from 'react'

const useStyles= makeStyles((theme)=>
createStyles({
    //image Area
    imageContainer: {
        width: "100%",
        height: "100px",
        justifyContent: "center",
        
      },
      imageStyle: {
        paddingTop: "56.25%", // 16:9
        width: "80%",
        borderRadius: 10,
        justifyContent: "center",
        marginLeft:10
      },
}))

export default function ImageArea(props) {
    const classes= useStyles();
    const { relatedMaterials} = props;
    
    const [imagePath, setImagePath] = useState(relatedMaterials[0])

    return(
        <Grid container>
            <Grid item md={2}>
                <Grid container style={{padding:20,margin:5, borderTopWidth:'5px', borderTopColor:'black'}}>
                    {
                        relatedMaterials.map((material)=>
                        <Grid item>
                              <CardMedia 
                        title='img' 
                        image={material}
                        style={{height:'50px', width:'50px', marginTop:'10px'}}
                        onClick={()=>setImagePath(material)}
                        />
                            </Grid>
                      
                        )
                    }
                </Grid>
            </Grid>
            <Grid item md={10}>
                     <div className={classes.imageContainer}>
        <CardMedia
          className={classes.imageStyle}
          image={imagePath}
          title="Material Name"
        />
      </div>
            </Grid>
        
        </Grid>
       
    )
}
