import { createMuiTheme } from "@material-ui/core/styles";

const Blue = "#13526f";
const Orange = "#FFBA60";
const Grey ='#868686';

export default createMuiTheme({
  palette: {
    common: {
      blue: `${Blue}`,
      orange: `${Orange}`,
      grey: `${Grey}`
    },
    primary: {
      main: `${Blue}`,
    },
    secondary: {
      main: `${Orange}`,
    },
  },
  typography: {
    tab: {
      fontFamily: "Raleway",
      textTransform: "none",
      fontWeight: 700,
      fontSize: "1rem",
      color:'white'
    },
    estimate: {
      fontFamily: "Pacifico",
      fontSize: "1rem",
      textTransform: "none",
      color:'white'
    },
    h2: {
      fontFamily: 'Raleway',
      fontWeight: 700,
      fontSize:'2.5rem',
      color:`${Blue}`,
      lineHeight:1.5
    },
    h3: {
      fontSize: '2.5rem'
    }
  },
});
