import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const providersSlice = createSlice({
    name: 'providers',
    initialState: {
        providers : null
    },
    reducers: {
        addProvidersSuccess: (state, action) => {
            state.providers=action.payload
        }
    }
})

export default providersSlice.reducer;

const {addProvidersSuccess} = providersSlice.actions;

export const loadProviders = () => async dispatch => {
    try {
        const res = await api.get('/providers');
        dispatch(addProvidersSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const addProvider = (values, userId,callback) => async dispatch => {
    const val= {...values,
        user_id:userId
    }
    try {
       await api.post('/providers',val);
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}
