import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import footerAdornment from "../../assets/img/svgs/FooterAdornment.svg";

import Grid from "@material-ui/core/Grid";

import { Link } from "react-router-dom";
import { Hidden } from "@material-ui/core";

import facebook from "../../assets/img/svgs/facebook.svg";
import twitter from "../../assets/img/svgs/twitter.svg";
import instagram from "../../assets/img/svgs/instagram.svg";

const useStyles = makeStyles((theme) => ({
  footer: {
    backgroundColor: theme.palette.grey.A400,
    width: "100%",
    zIndex: 1302,
    position: "relative",
    height:300
  },
  adornment: {
    width: "25em",
    verticalAlign: "bottom",
    [theme.breakpoints.down("md")]: {
      width: "21em",
    },
    [theme.breakpoints.down("xs")]: {
      width: "15em",
    },
  },
  mainContainer: {
    position: "absolute",
  },
  link: {
    color: "white",
    fontFamily: "Arial",
    fontSize: "0.75rem",
    fontWeight: "bold",
    textDecoration: "none",
  },
  gridItem: {
    margin: "3em",
  },
  icon: {
    height: "4em",
    width: "4em",
    [theme.breakpoints.down("sm")]: {
      height: "2.5em",
      width: "2.5em",
    },
    [theme.breakpoints.down("xs")]: {
      height: "0.6em",
      width: "0.6em",
    },
  },
  socialContainer: {
    position: "absolute",
    marginTop: "-6em",
    right: "1.5em",
  },
  footerImage:{
    height:150,
    width:150
  }
}));

export default function Footer(props) {
  //   const {  setValue, setSelectedIndex } = props;

  const classes = useStyles();

  return (
    <footer className={classes.footer}>
      <Hidden smDown>
        <Grid
          container
          justify="center"
          className={classes.mainContainer}
          spacing={2}
        >
          <Grid item className={classes.gridItem}>
            <Grid container direction="column" spacing={2}>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/"
                //   onClick={() => setValue(0)}
              >
                 <img src={instagram} className={classes.footerImage} />
              </Grid>
            </Grid>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Grid container direction="column" spacing={2}>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/services"
                //   onClick={() => {setValue(1); setSelectedIndex(0)}}
              >
                Services
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/customsoftware"
                //   onClick={() => {setValue(1); setSelectedIndex(1)}}
              >
                Custom Software Development
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/mobileapps"
                //   onClick={() => {setValue(1); setSelectedIndex(2)}}
              >
                Mobile App Development
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/websites"
                //   onClick={() => {setValue(1); setSelectedIndex(3)}}
              >
                Website Development
              </Grid>
            </Grid>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Grid container direction="column" spacing={2}>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/revolution"

                //   onClick={() => setValue(2)}
              >
                The Revolution
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/revolution"
                //   onClick={() => setValue(2)}
              >
                Vision
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/revolution"
                //   onClick={() => setValue(2)}
              >
                Technology
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/revolution"
                //   onClick={() => setValue(2)}
              >
                Process
              </Grid>
            </Grid>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Grid container direction="column" spacing={2}>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/about"
                //   onClick={() => setValue(3)}
              >
                About Us
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/about"
                //   onClick={() => setValue(3)}
              >
                History
              </Grid>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/about"
                //   onClick={() => setValue(3)}
              >
                Team
              </Grid>
            </Grid>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Grid container direction="column" spacing={2}>
              <Grid
                item
                className={classes.link}
                component={Link}
                to="/contact"
                //   onClick={() => setValue(4)}
              >
                Contact Us
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Hidden>

      {/* <img
        alt="black decorative splash"
        src={footerAdornment}
        className={classes.adornment}
      /> */}
      <Grid
        container
        justify="flex-end"
        spacing={2}
        className={classes.socialContainer}
      >
        <Grid
          item
          component={"a"}
          href="https://facebook.com"
          rel="noopener noreferrer"
          target="_blank "
        >
          <img alt="facebook link" src={facebook} className={classes.icon} />
        </Grid>
        <Grid
          item
          component={"a"}
          href="https://twitter.com"
          rel="noopener noreferrer"
          target="_blank "
        >
          <img alt="twiter link" src={twitter} className={classes.icon} />
        </Grid>
        <Grid
          item
          component={"a"}
          href="https://instagram.com"
          rel="noopener noreferrer"
          target="_blank "
        >
          <img alt="instagram link" src={instagram} className={classes.icon} />
        </Grid>
      </Grid>
    </footer>
  );
}
