import { Button, Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import React, { useState } from "react";

import { fade } from "@material-ui/core/styles";

import profile1 from "assets/img/faces/christian.jpg";
import profile2 from "assets/img/designs/design1.jpg";
import profile3 from "assets/img/designs/design2.jpg";
import profile4 from "assets/img/designs/design3.jpg";
import { CropRotate } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  imageBackground: {
    // width: "100%",
    height: 400,
  },
  filter: {
    height: "100%",
    width: "100%",
    "&:hover": {
      backgroundColor: "#000000de",
    },
    // backgroundColor:'#000000de'
  },
  icon: {
    fontSize: 100,
    marginBottom: 20,
  },
  name: {
    //   color:'yellow',
    marginBottom: 20,
    textAlign: "center",
    color: "white",
  },
  description: {
    marginLeft: 10,
    marginRight: 10,
    marginBottom: 20,
    color: "white",
    fontSize: 15,
    textAlign: "center",
  },
  learnMore: {
    backgroundColor: fade("#FFF", 0.35),
    paddingTop: 10,
    paddingBottom: 10,
    paddingLeft: 5,
    paddingRight: 5,
    width: "65%",
    marginLeft: "17%",
    justifyContent: "center",
    color: "white",
  },
  gridItem: {
    width: "100%",
  },
}));

const designs = [
  {
    name: "Abenezer",
    image: profile2,
    description:
      "You can write here details about one of your designs designs. You can give more details about what they do. ",
  },
  {
    name: "Tinsae",
    image: profile3,
    description:
      "You can write here details about one of your designs designs. You can give more details about what they do. ",
  },
  {
    name: "Amen",
    image: profile4,
    description:
      "You can write here details about one of your designs designs. You can give more details about what they do. ",
  },
];

const RenderDesign = (props) => {
  const [showHover, setShowHover] = useState(false);
  const { design, classes } = props;

  return (
    <div
      className={classes.filter}
      onMouseOver={() => setShowHover(true)}
      onMouseOut={() => setShowHover(false)}
    >
      <Grid
        container
        direction="column"
        justify="flex-end"
        alignContent="center"
        style={{ paddingTop: 50 }}
      >
        <Grid item className={classes.gridItem}>
          <Typography
            className={classes.description}
            style={{ color: "transparent" }}
          >
            {design.description}
          </Typography>
          {showHover ? (
            <React.Fragment>
              <Typography variant="h4" className={classes.name}>
                {design.name}
              </Typography>
              <Typography className={classes.description}>
                {design.description}
              </Typography>
            </React.Fragment>
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
};

export default function FeaturedArchitectsSection() {
  const classes = useStyles();
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      spacing={2}
      style={{ marginTop: 50, marginBottom: 100 }}
    >
      <Grid item>
        <Typography variant="h2" className={classes.title}>
          Featured Designs
        </Typography>
      </Grid>
      <Grid item>
        <Grid container justify="center" spacing={2}>
          {designs.map((design) => (
            <Grid item md={3}>
              <div
                style={{
                  backgroundImage: "url(" + design.image + ")",
                  backgroundSize: "cover",
                  height: "400px",
                  backgroundPosition: "top center",
                }}
                className={classes.imageBackground}
              >
                <RenderDesign design={design} classes={classes} />
              </div>
            </Grid>
          ))}
        </Grid>
      </Grid>
    </Grid>
  );
}
