import { loadContractors } from 'features/contractors/contractorsSlice';
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ContractorProfilePage from '../ContractorProfilePage/ContractorProfilePage';
import CreateContractorProfile from '../CreateContractorProfile/CreateContractorProfile';
import MyProfile from './MyProfile';

export default function MyContractorPage(props) {

    const {contractors} = useSelector(state => state.contractors);
    
    const user = useSelector(state => state.user)

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(
            loadContractors()
        ) 
        
    }, [])

    return (
      
        contractors !==null 
        
        ?
        
        contractors.some((contractor)=> contractor.user_id === user.user.id) ? 
        <MyProfile contractorId={contractors.filter((contractor)=>contractor.user_id===user.user.id)[0].id} /> 
        : 
       <CreateContractorProfile userId={user.user.id} history={props.history} />
        :
        <div>
            Loading
        </div>
    )
}
