import {  Grid, Typography, IconButton } from "@material-ui/core";
import React from "react";

import profile1 from "assets/img/faces/christian.jpg";
import profile2 from "assets/img/faces/avatar.jpg";
import profile4 from "assets/img/faces/card-profile1-square.jpg";
import profile5 from "assets/img/faces/card-profile2-square.jpg";
import profile7 from "assets/img/faces/card-profile4-square.jpg";
import profile8 from "assets/img/faces/card-profile6-square.jpg";
import profile10 from "assets/img/faces/kendall.jpg";
import profile11 from "assets/img/faces/marc.jpg";
import { makeStyles } from "@material-ui/styles";
import { useSelector } from "react-redux";
import { Facebook, LinkedIn, Twitter } from "@material-ui/icons";
import theme from "assets/jss/theme";
import { Link } from "react-router-dom";

const imageList = [
  profile1,
  profile2,
  profile4,
  profile5,
  profile7,
  profile8,
  profile10,
  profile11,
];

const useStyles = makeStyles((theme) => ({
  architectContainer: {
    marginTop: 100,
    boxShadow:
    "0 0px 0px -10px rgba(255, 152, 0, 0.28), 0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(255, 152, 0, 0.2)",
    marginLeft:200,
    marginRight:10,
    paddingTop:30,
    paddingBottom:30
    

  },
  imageContainer: {
    // width: "70%",
    margin: 0,

    // padding: 0,
    marginLeft:'-100px',
    backgroundColor:'transparent',
    borderRadius:'50%',
    paddingBottom: 0,
    boxShadow:
      "20px 12px 20px -10px rgba(19, 82, 111, 0.28), 0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(255, 152, 0, 0.2)",
  },

  imageStyle: {
    height: "auto",
    width: "100%",
    margin: 0,
    padding: 0,
    borderRadius:'50%',
    backgroundColor:'transparent'
  },
  detailContainer: {
    textAlign: "center",
    textDecoration: "none",
  },
  name: {
    color: "#303030",
    fontWeight: "bold",
    fontSize: "1.5rem",
  },
  professionalLevel: {
    color: "#424242",
    fontSize: "bold",
  },
  descriptionText: {
    color: "#bdbdbd",
    fontWeight: "100",
    paddingLeft: 20,
    paddingRight: 20,
  },
  socialIcons: {
    maxWidth: "24px",
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
  },
  socialLine: {
    marginTop: "1rem",
    textAlign: "center",
    padding: "0",
  },
}));

export default function ArchitectsList() {
  const classes = useStyles();

  const architects = useSelector((state) => state.architects);
  if (architects.architects) {
    return (
      <Grid container justify="flex-start">
        {architects.architects.map((architect, index) => (
          <Grid
            key={architect.id}
            item
            xs={10}
            sm={10}
            md={4}
            className={classes.architectContainer}
          >
            <Grid container justify='space-between' alignItems="center">
              <Grid
                item
                xs={10}
                sm={6}
                md={4}
                component={Link}
                to={`/architects/${architect.id}`}
              >
                <div className={classes.imageContainer}>
                  <img
                    src={imageList[index % 8]}
                    className={classes.imageStyle}
                    alt='...'
                  />
                </div>
              </Grid>
              <Grid
                item
                xs={10}
                sm={6}
                md={6}
               
              >
                <Grid component={Link} to={`/architects/${architect.id}`}  className={classes.detailContainer}>
                  <Typography className={classes.name}>
                    {architect.name}
                  </Typography>
                  <Typography className={classes.professionalLevel}>
                    {architect.professional_level}
                  </Typography>
                  <p className={classes.descriptionText}>
                    {architect.short_description}
                  </p>
                </Grid>

                <div className={classes.socialLine}>
                  <IconButton>
                    <Twitter color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <Facebook color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <LinkedIn color="primary" className={classes.socialIcons} />
                  </IconButton>
                </div>
              </Grid>
            </Grid>
          </Grid>
        ))}
      </Grid>
    );
  } else {
    return <Typography variant="h3">Empty</Typography>;
  }
}
