import {
  Button,
  Grid,
  makeStyles,
  TextField,
  Typography,
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  FormLabel,
} from "@material-ui/core";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import Parallax from "components/Parallax/Parallax";
import React, { useState } from "react";
import classNames from "classnames";
import { container, title } from "assets/jss/commons";

import imagesStyle from "assets/jss/imagesStyles";
import { renderTextField } from "components/ReduxInput/ReduxInput";
import { Field, reduxForm } from "redux-form";
import { renderSelectField } from "components/ReduxInput/ReduxInput";

import { addContractor } from "features/contractors/contractorsSlice";

import { countryList } from "assets/data";
import imageBackground from "assets/img/backgrounds/landing-bg.jpg";
import { useDispatch } from "react-redux";
import { loadContractors } from "features/contractors/contractorsSlice";
import { renderCheckbox } from "components/ReduxInput/ReduxInput";

import ImageUploader from "components/ImageUpload/ImageUploader";


const useStyles = makeStyles({
  container: {
    ...container,
  },
  profile: {
    textAlign: "center",
    "& img": {
      maxWidth: "160px",
      width: "100%",
      margin: "0 auto",
      transform: "translate3d(0, -50%, 0)",
    },
  },
  description: {
    margin: "1.071rem auto 0",
    maxWidth: "600px",
    color: "#999",
    textAlign: "center !important",
  },
  name: {
    marginTop: "-80px",
  },
  ...imagesStyle,
  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",
    paddingBottom: 50,
  },
  mainRaised: {
    margin: "-60px 30px 20px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  },
  title: {
    ...title,
    display: "inline-block",
    position: "relative",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none",
    marginLeft: "20px",
  },
  titleSecondary: {
    // ...title,
    display: "inline-block",
    position: "relative",
    // marginTop: "30px",
    // minHeight: "32px",
    textDecoration: "none",
    // marginLeft: "20px",
  },
  formContainer: {
    marginLeft: "50px",
  },
  textField: {
    margin: 20,
    // marginTop: "20px",
    width: "80%",
  },
  selectField: {
    margin: 20,
    marginTop: "20px",

    width: 300,
  },
});

let CreateContractorForm = reduxForm({
  form: "createContractor",
});

CreateContractorForm = (props) => {
  const {
    handleSubmit,
    pristine,
    reset,
    submitting,
    classes,
    contractorType,
    handleChange,
  } = props;
  return (
    <form onSubmit={handleSubmit}>
      <div className={classes.formContainer}>
        <Grid container spacing={5} alignItems="center">
          <Grid item md={12}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Contractor Type</FormLabel>
              <RadioGroup
                aria-label="contractor_type"
                name="contractor_type1"
                value={contractorType}
                onChange={handleChange}
              >
                <Grid container>
                  <Grid item>
                    <FormControlLabel
                      value="individual"
                      control={<Radio />}
                      label="Individual"
                    />
                  </Grid>
                  <Grid item>
                    <FormControlLabel
                      value="company"
                      control={<Radio />}
                      label="Company"
                    />
                  </Grid>
                </Grid>
              </RadioGroup>
            </FormControl>
          </Grid>
        </Grid>

        {contractorType === "individual" ? (
          <React.Fragment>
             <Typography
                  variant="h4"
                  color="primary"
                  className={classes.titleSecondary}
                >
                  Basic Info
                </Typography>


<Grid container>
  <Grid item md={8}>
  <Grid container>
            <Grid item md={5}>
                <Field
                  name="name"
                  component={renderTextField}
                  label="Name"
                  className={classes.textField}
                />
              </Grid>
              <Grid item md={5}>
                <Field
                  name="age"
                  component={renderTextField}
                  label="Age"
                  type="number"
                  className={classes.textField}
                />
              </Grid>
              <Grid item md={5}>
                <Field
                  name="gender"
                  component={renderSelectField}
                  className={classes.selectField}
                  label="Gender"
                >
                  <option value="" />
                  <option value={"Male"}>Male</option>
                  <option value={"Female"}>Female</option>
                </Field>
              </Grid>
              <Grid item md={5}>
                <Field
                  name="nationality"
                  component={renderSelectField}
                  className={classes.selectField}
                  label="Nationality"
                >
                  <option value="" />

                  {countryList.map((country) => (
                    <option key={country} value={country}>
                      {country}
                    </option>
                  ))}
                </Field>
              </Grid>
            </Grid>
  </Grid>
  <Grid item md={3} justify='center' alignContent='center' alignItems='center'>
            <ImageUploader />
          </Grid>
</Grid>

            



<Typography
                  variant="h4"
                  color="primary"
                  className={classes.titleSecondary}
                >
                  Professional background
                </Typography>
            <Grid container alignItems="center">
             
              
              <Grid item md={12}>
                <Grid container>
                  <Grid item>
                    <Field
                      name="civil"
                      component={renderCheckbox}
                      label="Civil"
                      className={classes.textField}
                    />
                  </Grid>
                  <Grid item>
                    <Field
                      name="electrical"
                      component={renderCheckbox}
                      label="Electrical"
                      className={classes.textField}
                    />
                  </Grid>
                  <Grid item>
                    <Field
                      name="mechanical"
                      component={renderCheckbox}
                      label="Mechanical"
                      className={classes.textField}
                    />
                  </Grid>
                  <Grid item>
                    <Field
                      name="hvac"
                      component={renderCheckbox}
                      label="HVAC"
                      className={classes.textField}
                    />
                  </Grid>
                </Grid>
              </Grid>
              <Grid item md={4}>
                <Field
                  name="school"
                  component={renderTextField}
                  className={classes.textField}
                  label="school"
                />
              </Grid>
              <Grid item md={3}>
                <Field
                  name="graduate_year"
                  component={renderTextField}
                  className={classes.textField}
                  label="Year of Graduation"
                  type="number"
                />
              </Grid>
              <Grid item md={3}>
                <Field
                  name="CGPA"
                  component={renderTextField}
                  className={classes.textField}
                  label="CGPA"
                  type="number"
                />
              </Grid>
              <Grid item md={4}>
                <Field
                  name="personality_type"
                  component={renderTextField}
                  className={classes.textField}
                  label="Personality Type"
                />
              </Grid>
              <Grid item md={4}>
                <Field
                  name="aptitude"
                  component={renderTextField}
                  className={classes.textField}
                  label="Aptitude"
                />
              </Grid>
              <Grid item md={4}>
                <Field
                  name="location"
                  component={renderTextField}
                  className={classes.textField}
                  label="Location"
                />
              </Grid>
            </Grid>
          </React.Fragment>
        ) : null}
        {
          contractorType === 'company' ? 
          <React.Fragment>
             <Grid container spacing={5} alignItems="center">
             <Grid item md={4}>
            <Field
              name="name"
              component={renderTextField}
              label="Name"
              className={classes.textField}
            />
          </Grid>
          <Grid item md={3}>
            <Field
              name="tin_number"
              component={renderTextField}
              label="Tin Number"
              className={classes.textField}
            />
          </Grid>
          <Grid item md={4}>
            <Field
              name="location"
              component={renderTextField}
              className={classes.textField}
              label="Location"
            />
          </Grid>
             </Grid>

          </React.Fragment>:
          null
        }
        
      
        <Grid container spacing={2} alignItems="center">
          <Grid item md={3}>
            <Field
              name="fb_account"
              component={renderTextField}
              className={classes.textField}
              label="Facebook account link"
            />
          </Grid>
          <Grid item md={3}>
            <Field
              name="twitter_account"
              component={renderTextField}
              className={classes.textField}
              label="Twitter account link"
            />
          </Grid>
          <Grid item md={3}>
            <Field
              name="instagram_account"
              component={renderTextField}
              className={classes.textField}
              label="Instagram account link"
            />
          </Grid>
          <Grid item md={3}>
            <Field
              name="email"
              component={renderTextField}
              className={classes.textField}
              label="Email Address"
              type='email'
            />
          </Grid>
          <Grid item md={3}>
            <Field
              name="website"
              component={renderTextField}
              className={classes.textField}
              label="Website"
              type='url'
            />
          </Grid>
        </Grid>

        <Grid container spacing={5} alignItems="center">
          <Grid item md={11}>
            <Field
              name="short_description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About Self"
            />
          </Grid>
        </Grid>

        <Button type="submit" color="primary" size="large">
          Submit
        </Button>
      </div>
    </form>
  );
};

CreateContractorForm = reduxForm({
  form: "createContractor",
})(CreateContractorForm);

export default function CreateContractorProfile(props) {
  const classes = useStyles();

  const [contractorType, setContractorType] = useState("individual");

  const handleChange = (event) => {
    setContractorType(event.target.value);
  };

  const dispatch = useDispatch();

  const submit = (values) => {
    const val={...values, contractor_type:contractorType}
    dispatch(addContractor(val, props.userId,()=>dispatch(loadContractors()) ));
    // console.log(values);
  };

  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  setTimeout(function () {
    setCardAnimation("");
  }, 700);

  return (
    <div>
      <Header
        color="transparent"
        brand="Nidf"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white",
        }}
        // {...rest}
      />

      <Parallax small filter image={imageBackground} />
      <div className={classNames(classes.main, classes.mainRaised)}>
        <Grid container justify="flex-start">
          <Grid item xs={12} sm={12} md={11}>
            <Typography className={classes.title} variant="h3">
              Create Your profile
            </Typography>

            <CreateContractorForm
              onSubmit={submit}
              classes={classes}
              contractorType={contractorType}
              handleChange={handleChange}
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
