const RenderArchitectsSelected = (props) => {
  const {
    item,
    classes,
    index,
    architects,
    setArchitectItems,
    selected,
    setSelected,
    removed,
  } = props;

  const onCloseButton = () => {
    const newArchitects = [...architects, removed];
    const newSelectResult = selected.filter(
      (select) => select.id !== removed.id
    );
    setArchitectItems(newArchitects);
    setSelected(newSelectResult);
  };

  return (
    <Grid
      container
      spacing={2}
      justify="center"
      alignItems="center"
      style={{
        paddingRight: 10,
        paddingLeft: 10,
        marginTop:20,
        backgroundColor: "white",
      }}
    >
      <Grid item md={2}>
        <img
          src={item.image}
          width={100}
          height={100}
          style={{ borderRadius: "50%" }}
        />
      </Grid>
      <Grid item md={7}>
        <Grid direction="column" container>
          <Typography variant="h5" color="primary" style={{padding:5}}>
            {item.name}
          </Typography>
        </Grid>
        <Grid direction="column" container>
          <Typography style={{padding:5}}>{item.description}</Typography>
        </Grid>
      </Grid>
      <Grid item md={2}>
        
          <Typography style={{width:'100%'}} color="textSecondary">{item.type}</Typography>
         
         
        </Grid>

      <Grid item md={1}>
        
          <IconButton onClick={onCloseButton}>
            <Close color="error" className={classes.closeIcons} />
          </IconButton>
        
      </Grid>
    </Grid>
  );
};


