function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const typeA = a.user_type.toUpperCase();
  const typeB = b.user_type.toUpperCase();

  let comparison = 0;
  if (typeA > typeB) {
    comparison = 1;
  } else if (typeA < typeB) {
    comparison = -1;
  }
  return comparison;
}

const RenderMaterialsSelected = (props) => {
  const {
    item,
    classes1,
    classes,
    index,
    providerMaterials,
    setMaterialItems,
    selected,
    setSelected,
    removed,
  } = props;

  const onCloseButton = () => {
    const newProviderMaterials = [...providerMaterials, removed];
    const newSelectResult = selected.filter(
      (select) => select.id !== removed.id
    );
    setMaterialItems(newProviderMaterials);
    setSelected(newSelectResult);
  };
  const materialsImage = [
    material1,
    material2,
    material3,
    material4,
    material5,
  ];

  return (
    <Grid key={item.id} contractor md={12} className={classes.container}>
      <Grid
        container
        justify="flex-start"
        // component={Link}
        // to={`/contractors/${item.id}`}
        style={{ textDecoration: "none" }}
      >
        <Grid item md={6}>
          <div className={classes.imageContainer}>
            <img
              src={materialsImage[index % 5]}
              className={classes1.media}
              alt="..."
            />
          </div>
        </Grid>
        <Grid item md={6}>
          <div className={classes.detailContainer}>
            <div>
              <CardContent className={classes1.cardFooter}>
                <Typography color="primary" variant="h5" component="h2">
                  {item.name}
                </Typography>

                <Typography variant="body2" color="primary" component="p">
                  Price: {item.price}ETB
                </Typography>
                <Typography
                  variant="body2"
                  component="p"
                  style={{ color: "black" }}
                >
                  {item.location}
                </Typography>
                <Typography
                  variant="body2"
                  color="textSecondary"
                  component="p"
                  style={{ color: "black" }}
                >
                  {item.deliverable}
                </Typography>
              </CardContent>
            </div>
          </div>
        </Grid>
        <Grid item>
          <div
            className={classes.closeLine}
            style={{
              borderLeftWidth: "2px",
              borderLeftColor: "blue",
            }}
          >
            <IconButton onClick={onCloseButton}>
              <Close color="error" className={classes.closeIcons} />
            </IconButton>
          </div>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default function SelectedSection(props) {
  const classes = useStyles();

  const classes1 = useStylesMaterial();

  const {
    items,
    architects,
    providers,
    contractors,
    providerMaterials,
    setArchitectItems,
    setContractorItems,
    setProviderItems,
    setMaterialItems,
    setSelected,
  } = props;
  // const items=[]

  const arraySort = items.sort(compare);

  console.log("arraySort", arraySort);

  if (items) {
    return (
      <Grid container justify="center">
        <Droppable droppableId="selectedDroppable">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
            >
              {items.length > 0 ? (
                items.map((item, index) =>
                <React.Fragment>
                  {item.user_type === "architect" ? (
                    
                    <RenderArchitectsSelected
                      item={item}
                      architects={architects}
                      setArchitectItems={setArchitectItems}
                      selected={items}
                      setSelected={setSelected}
                      key={item.id}
                      classes={classes}
                      index={index}
                      removed={item}
                    />
                    ) : item.user_type === "provider" ? (
                    <RenderProvidersSelected
                      item={item}
                      providers={providers}
                      setProviderItems={setProviderItems}
                      selected={items}
                      setSelected={setSelected}
                      key={item.id}
                      classes={classes}
                      index={index}
                      removed={item}
                    />
                    ) : item.user_type === "contractor" ? (
                    <RenderContractorsSelected
                      item={item}
                      contractors={contractors}
                      setContractorItems={setContractorItems}
                      selected={items}
                      setSelected={setSelected}
                      key={item.id}
                      classes={classes}
                      index={index}
                      removed={item}
                    />
                    ) : item.user_type === "provider_material" ? (
                    <RenderMaterialsSelected
                      item={item}
                      providerMaterials={providerMaterials}
                      setMaterialItems={setMaterialItems}
                      selected={items}
                      setSelected={setSelected}
                      key={item.id}
                      classes={classes}
                      classes1={classes1}
                      index={index}
                      removed={item}
                    />
                ) : null}
              
                </React.Fragment>
                  
                )
              ) : (
                <div style={{ height: '100%', marginTop:100, marginBottom:100 }}>
                  <Grid container justify='center' alignItems='center'>
                    <Grid item>
                      <Typography> Drag and Drop your Choices here</Typography>
                     
                    </Grid>
                  </Grid>
                </div>
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <Button
                        component={Link}
                        to="/project_empty"
                        variant="outlined"
                        // color={items.length > 0 ? "primary" : "secondary" } 
                        disabled={items.length > 0 ? false: true}
                        style={{
                          // backgroundColor:'red',
                          margin: 20,
                          color: "white",
                          backgroundColor: items.length > 0 ? theme.palette.primary.dark : theme.palette.primary.light,
                        }}
                      >
                        Create My Project
                      </Button>
      </Grid>
    );
  } else {
    return <Typography variant="h3"> </Typography>;
  }
}
