import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import Header from 'components/Header/Header';
import HeaderLinks from 'components/Header/HeaderLinks';
import React from 'react';
import DescriptionSection from './sections/DescriptionSection';
import NetsaDesignSection from './sections/NetsaDesignSection';
import ProjectsSection from './sections/ProjectsSection';


const useStyles=makeStyles((theme)=>({
    container:{
        backgroundColor: "#f4f7fc"
    }
}))

const user={
    name:'Dagim Temesgen'
}

const projects=[
    {
        name:'Project One',
        progress:45,
        increment:0.05
    },
    {
        name:'Project Two',
        progress:72,
        increment:-0.05
    },
    {
        name:'Project Three',
        progress:45,
        increment:0.05
    },
    {
        name:'Project Four',
        progress:72,
        increment:-0.05
    },
    {
        name:'Project Five',
        progress:45,
        increment:0.05
    },
    {
        name:'Project Six',
        progress:72,
        increment:-0.05
    },
    {
        name:'Project Seven',
        progress:45,
        increment:0.05
    },
    {
        name:'Project Eight',
        progress:72,
        increment:-0.05
    },
    {
        name:'Project Nine',
        progress:72,
        increment:-0.05
    }

    
]
export default function ProfilePage(props) {
    const classes= useStyles();
    return (
       <React.Fragment>
           <Header
        color="dark"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
        // style={{backgroundColor:'red'}}
      />
      <Grid container direction='column'>
          <Grid item>
              <DescriptionSection user={user}/>
          </Grid>
          <Grid item>
              <Grid container style={{backgroundColor: "#f4f7fc"}}>
                  <Grid item md={9}>
                  <ProjectsSection projects={projects} history={props.history}/>
                  </Grid>
                  <Grid item md={3}>
                    <NetsaDesignSection />
                  </Grid>
              </Grid>
              
          </Grid>
      </Grid>
       </React.Fragment>
    )
}
