import React from "react";

import { makeStyles } from "@material-ui/core/styles";

import Grid from "@material-ui/core/Grid";

import { Link } from "react-router-dom";
import { Hidden, IconButton, Typography } from "@material-ui/core";

import instagram from "../../assets/img/svgs/instagram.svg";
import { Facebook, LinkedIn, Twitter } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  footer: {
    backgroundColor: theme.palette.grey.A400,
    width: "100%",
    zIndex: 1302,
    position: "relative",
    height: "400px",
    marginTop:20,
    zIndex:'1300'
  },
  adornment: {
    width: "25em",
    verticalAlign: "bottom",
    [theme.breakpoints.down("md")]: {
      width: "21em",
    },
    [theme.breakpoints.down("xs")]: {
      width: "15em",
    },
  },
  mainContainer: {
    position: "absolute",
  },
  link: {
    color: "white",
    fontFamily: "Arial",
    fontSize: "0.75rem",
    // fontWeight: "bold",
    textDecoration: "none",
  },
  gridItem: {
    margin: "3em",
  },
  icon: {
    height: "4em",
    width: "4em",
    [theme.breakpoints.down("sm")]: {
      height: "2.5em",
      width: "2.5em",
    },
    [theme.breakpoints.down("xs")]: {
      height: "0.6em",
      width: "0.6em",
    },
  },
  socialContainer: {
    position: "absolute",
    marginTop: "-6em",
    right: "1.5em",
  },
  footerImage: {
    height: 150,
    width: 150,
  },
  paragraph: {
    color: "white",
    fontSize: 15,
  },
  socialIcons: {
    maxWidth: "24px",
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
    color: "white",
  },
  socialLine: {
    marginTop: "1rem",
    textAlign: "flex-start",
    padding: "0",
  },
  title: {
    fontWeight: "bold",
    fontSize: 25,
  },
}));

