import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const providerDetailsSlice = createSlice({
    name: 'providerDetails',
    initialState: {
        providerDetails : null
    },
    reducers: {
        loadProviderDetailsSuccess: (state, action) => {
            state.providerDetails=action.payload
        }
    }
})

export default providerDetailsSlice.reducer;

const {loadProviderDetailsSuccess} = providerDetailsSlice.actions;

export const loadProviderDetails = (providerId) => async dispatch => {
    try {
        const res = await api.get('/providers?id='+providerId);
        console.log("providers Details",res.data)
        dispatch(loadProviderDetailsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const editProviderDetails = (providerId,values,callback) => async dispatch => {
    try {
        const res = await api.patch('/providers/'+providerId,values );
        console.log("providers Details",res.data)
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}