import React from "react";
import "../node_modules/react-vis/dist/style.css";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import { ThemeProvider } from "@material-ui/styles";
import theme from "assets/jss/theme";
import { Provider } from "react-redux";
import store from "app/store";
import LoginPage from "views/AuthenticatePages/LoginPage/LoginPage";
import SignupPage from "views/AuthenticatePages/SignUpPage/SignupPage";
import Parallax from "components/Parallax/Parallax";

import image from "assets/img/backgrounds/bg7.jpg";
import MarketPrices from "views/MarketPrices/MarketPrices";

import ArchitectListPage from "views/architects/ArchitectListPage/ArchitectListPage";
import ArchitectProfilePage from "views/architects/ArchitectProfilePage/ArchitectProfilePage";
import CreateArchitectProfile from "views/architects/CreateArchitectProfile/CreateArchitectProfile";
import MyArchitectPage from "views/architects/MyArchitectPage/MyArchitectPage";

import ContractorListPage from "views/contractors/ContractorListPage/ContractorListPage";
import ContractorProfilePage from "views/contractors/ContractorProfilePage/ContractorProfilePage";
import CreateContractorProfile from "views/contractors/CreateContractorProfile/CreateContractorProfile";
import MyContractorPage from "views/contractors/MyContractorPage/MyContractorPage";

import ProviderListPage from "views/providers/ProviderListPage/ProviderListPage";
import ProviderProfilePage from "views/providers/ProviderProfilePage/ProviderProfilePage";
import CreateProviderProfile from "views/providers/CreateProviderProfile/CreateProviderProfile";
import MyProviderPage from "views/providers/MyProviderPage/MyProviderPage";
import LandingPage from "views/LandingPage/LandingPage";
import ProvidersItemsPages from "views/providers/ProvidersItemsPages/ProvidersItemsPages";
import MaterialsListPage from "views/materials/MaterialsListPage/MaterilasListPage";
import MaterialPage from "views/materials/MaterialPage/MaterialPage";

import CreateProjectPage from "views/projects/CreateProjectPage/CreateProjectPage";
import RecommendationsPage from "views/projects/RecommendationsPage/RecommendationsPage";
import DNDElement from "components/DNDElement/DNDElement";
import Footer from "components/Footer/Footer";
import FooterNew from "components/Footer/FooterNew";
import ProfilePage from "views/ProfilePage/ProfilePage";
import ProjectPage from "views/ProjectPage/ProjectPage";
import ProjectEmptyPage from "views/ProjectPageEmpty/ProjectEmptyPage";


export default function App() {
  return (
    <Provider store={store}>
      <ThemeProvider theme={theme}>
        <BrowserRouter>
          <Switch>

            <Route exact path='/my-architect-page' component={MyArchitectPage} />
            <Route exact path='/create-architect-profile/:userId' component={CreateArchitectProfile} />
            <Route exact path="/architects/:architectId" component={ArchitectProfilePage} /> 
            <Route exact path="/architects-list" component={ArchitectListPage} />


            <Route exact path='/my-contractor-page' component={MyContractorPage} />
            <Route exact path='/create-contractor-profile/:userId' component={CreateContractorProfile} />
            <Route exact path="/contractors/:contractorId" component={ContractorProfilePage} /> 
            <Route exact path="/contractors-list" component={ContractorListPage} />


            <Route exact path='/my-provider-page' component={MyProviderPage} />
            <Route exact path='/create-provider-profile/:userId' component={CreateProviderProfile} />
            <Route exact path="/providers/:providerId" component={ProviderProfilePage} /> 
            <Route exact path="/providers-list" component={ProviderListPage} />
            <Route exact path="/materialDescription/:materialId" component={MaterialPage} />

            <Route exact path='/materials' component={MaterialsListPage} />
            <Route exact path="/market-prices" component={MarketPrices} />
            <Route exact path="/signup" component={SignupPage} />
            <Route exact path="/login" component={LoginPage} />

            <Route exact path='/recommendations' component={RecommendationsPage} />
            <Route exact path='/create-project' component={CreateProjectPage} />
            <Route exact path="/landing-page" component={LandingPage} />

            <Route exact path="/profile" component={ProfilePage} />
            <Route exact path="/project_empty" component={ProjectEmptyPage} />
            <Route exact path="/project" component={ProjectPage} />

            {/* <Route exact path='/try-component' component={ImageUploader} /> */}

            

            <Route path="/" component={LandingPage}/>
            
          </Switch>
       <FooterNew />
        </BrowserRouter>
      </ThemeProvider>
    </Provider>
  );
}
