import { Card, Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles'
import CustomizedStepper from 'components/Stepper/Stepper';
import React from 'react'

const useStyles=makeStyles((theme)=>({
    progressSectionContainer:{
        backgroundColor:'white',
        width:'100%',
        padding:20
    },
    behindSchedule:{
        color:'red',
        marginLeft:3
    },
    daysLeft:{
        color:'green',
        marginLeft:3
    },
    countdownContainer:{
        border:1,
        borderWidth:'10px',
        borderColor:'red'
    },
    percentage:{
        backgroundColor:theme.palette.primary.main,
        color:'white',
        padding:'10px',
        borderRadius:10,
        fontSize:10
    }

}))

export default function ProgressSection() {
    const classes=useStyles();
    return (
        <React.Fragment>
           
            <Card className={classes.progressSectionContainer}>
          
            <Grid container direction='column' spacing={2}>
                <Grid item>
                <Grid container justify='space-between'>
                    <Grid item>
                        <Typography style={{fontWeight:'bold'}}>Progress</Typography>
                    </Grid>
                    <Grid item>
                        <Grid container>
                            <Grid item>
                                Status:
                            </Grid>
                            <Grid item className={classes.behindSchedule}>
                                Behind Schedule
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item>
                    <Grid container borderLeft={1} borderColor='red' className={classes.countdownContainer}>
                            <Grid item>
                                Countdown:{' '} 
                            </Grid>
                            <Grid item className={classes.daysLeft}>
                                22 days Left
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item className={classes.percentage}>
                        55 percent
                    </Grid>
                    
                </Grid>
                </Grid>
                <Grid item>
                <CustomizedStepper />
                </Grid>
            </Grid>

                

                
            </Card>
           
        </React.Fragment>
    )
}
