import { Grid, Typography } from "@material-ui/core";
import { createStyles, makeStyles } from "@material-ui/styles";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import React, { useEffect } from "react";

import { containerFluid } from "assets/jss/commons";

import material1 from "assets/img/materials/material1.jpg";
import material2 from "assets/img/materials/material2.jpg";
import material3 from "assets/img/materials/material3.jpg";
import material4 from "assets/img/materials/material4.jpg";
import material5 from "assets/img/materials/material5.jpg";
import ImageArea from "./sections/ImageArea";
import DescriptionArea from "./sections/DescriptionArea";
import ActionsArea from "./sections/ActionsArea";
import { useDispatch, useSelector } from "react-redux";
import { loadMaterialDetails } from "features/providers/materialDetailsSlice";
import GraphArea from "./sections/GraphArea";

const useStyles = makeStyles((theme) =>
  createStyles({
    container: {
      // ...containerFluid,
      zIndex: "12",
      //   backgroundColor: "#e9ecec",
      paddingTop: "30px",
      paddingBottom: "20px",
      paddingLeft: "20px",
    },
  })
);

const relatedMaterials = [
  material1,
  material2,
  material3,
  material4,
  material5,
];

export default function MaterialPage({ match }) {
  const classes = useStyles();

  const dispatch = useDispatch();

  const { materialDetails } = useSelector((state) => state.materialDetails);

  useEffect(() => {
    // console.log('hi')
    dispatch(loadMaterialDetails(match.params.materialId));
    console.log(materialDetails);
  }, []);

  return (
    <React.Fragment>
      <Header
        color="dark"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        // fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
      />
      <div className={classes.container}>
        <Grid container justify="flex-start">
          <Grid item md={4}>
            <Grid container direction="column">
              <Grid item>
                <ImageArea relatedMaterials={relatedMaterials} />
              </Grid>
              <Grid item>
                
                <GraphArea />
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={4}>
            {materialDetails !== null &&
            materialDetails.materialDetails !== null ? (
              <DescriptionArea material={materialDetails[0]} />
            ) : null}
          </Grid>

          <Grid item md={4}>
            <ActionsArea relatedMaterials={relatedMaterials} />
          </Grid>
        </Grid>
      </div>
    </React.Fragment>
  );
}
