import React, { useEffect } from "react";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import Parallax from "components/Parallax/Parallax";

import classNames from "classnames";
import {
  makeStyles,
  Slide,
} from "@material-ui/core/";

import { useDispatch } from "react-redux";

import backgroundImage from "assets/img/backgrounds/landing-bg.jpg";

import { loadContractorDetails } from "features/contractors/contractorDetailsSlice";
import { loadContractorDesigns } from "features/contractors/contractorDesignsSlice";
import ContractorDescriptionSection from '../ContractorProfilePage/sections/ContractorDescriptionSection';
import ContractorDesignsSection from '../ContractorProfilePage/sections/ContractorDesignsSection';
import AddDesignModal from '../ContractorProfilePage/sections/AddDesignModal';


const useStyles = makeStyles((theme) => ({
  container: {
    paddingRight: "0",
    paddingLeft: "0",

    paddingTop: "0px",
    marginRight: "auto",
    marginLeft: "auto",
    // width: "100%",
  },

  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",

    paddingBottom:'20px'
  },
  mainRaised: {
    margin: "-60px 30px 0px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

Transition.displayName = "Transition";

export default function MyProfile(props) {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadContractorDetails(props.contractorId));
    dispatch(loadContractorDesigns(props.contractorId));
  }, [props.contractorId,dispatch]);

  return (
    <React.Fragment>
      <Header
        color="transparent"
        brand="Nidf"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white",
        }}
      />
      <Parallax small filter image={backgroundImage} />

      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <ContractorDescriptionSection />
          <AddDesignModal />
          <ContractorDesignsSection />
        </div>
      </div>
    </React.Fragment>
  );
}
