import { Button, Card, CardMedia, Grid, Typography } from '@material-ui/core';
import { ShoppingCart } from '@material-ui/icons';
import { createStyles, makeStyles } from '@material-ui/styles';
import theme from 'assets/jss/theme';
import React from 'react'

const useStyles= makeStyles((theme)=>
createStyles({
     // action area
     actionsContainer: {
        paddingLeft: "20px",
        paddingRight: "20px",
      },
      relatedMaterialImage:{
          // paddingTop: "56.25%", // 16:9
          height:'150px',
          marginTop:10,
          marginRight:10,
          borderRadius: 2,
          justifyContent: "center",
      }
}))

export default function ActionsArea (props){
    const classes= useStyles();
    const { relatedMaterials } = props;
    return (
      <div className={classes.actionsContainer}>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <Card style={{paddingRight:20,paddingLeft:20,paddingTop:10,paddingBottom:10}}>
            <Button
              variant="outlined"
              color="primary"
              startIcon={<ShoppingCart />}
            >
              Add to Cart
            </Button>
            <div style={{marginTop:10}}/>
            <Button 
            variant='text'
            style={{color: theme.palette.primary.main, fontSize:'0.8em'}}>
                  Request Delivery
            </Button>
          </Card> 
          </div>
          <Typography style={{textDecoration:'underline', color:theme.palette.primary.main, marginTop:20}}>
              Other Related Materials
          </Typography>
          {/* <div style={{ display: "flex", justifyContent: "center" }}> */}
          <Grid container justify='flex-start' style={{marginLeft:20,marginRight:20}}>
               {
                  relatedMaterials.map((material)=>
                  <Grid item md={5}>
                        <CardMedia 
                  className={classes.relatedMaterialImage}
                  image={material}
                  title="Material Name"
                  />
                      </Grid>
                
                      
                  )
              }
          </Grid>
             
          {/* </div> */}
       
      </div>
    );
  };