import React from 'react';
import {
    Grid,
    makeStyles,
    Typography,
  } from "@material-ui/core";

const useStyles=makeStyles((theme)=>({
    headerItem: {
        backgroundColor: theme.palette.primary.main,
        padding:20,
        
      },
      headerText: {
          color:theme.palette.secondary.main
      },
}))

export default function TableHeader() {
    const classes= useStyles()
    return (
        <React.Fragment>
            <Grid item xs={6} sm={3} className={classes.headerItem}>
              <Typography className={classes.headerText}>Item</Typography>
            </Grid>
            <Grid item xs={6} sm={3} className={classes.headerItem}>
              <Typography className={classes.headerText}>Price</Typography>
            </Grid>
            <Grid item xs={6} sm={3} className={classes.headerItem}>
              <Typography className={classes.headerText}>Change</Typography>
            </Grid>
            <Grid item xs={6} sm={3} className={classes.headerItem}>
              <Typography className={classes.headerText}>Availability</Typography>
            </Grid>
        </React.Fragment>
    )
}
