import {  Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import React, { useEffect } from "react";

import classNames from "classnames";

import { container } from "assets/jss/commons";

import Parallax from "components/Parallax/Parallax";

import backgroundImage from 'assets/img/backgrounds/landing-bg.jpg'
import { useDispatch, useSelector } from "react-redux";
import { loadProviderMaterialsAll } from "features/providers/providerMaterialsSlice";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import MaterialsList from "./sections/MaterialsList";
import FilteringSection from "./sections/FilteringSection";




const useStyles = makeStyles((theme) => ({
  container: {
    zIndex: "12",
    color: "#FFFFFF",
    paddingTop: "200px",
    marginLeft: "1rem",
    position: "absolute",
    ...container,

    // paddingLeft: "200px",
  },
  main: {
    background: "#FFFFFF",
    position: "relative",
    zIndex: "3",
  },
  mainRaised: {
    margin: "-60px 30px 60px",
    borderRadius: "6px",
    boxShadow:
      "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
  }
}));



const MaterialsListPage = (props) => {
  const classes = useStyles();

  const dispatch = useDispatch();


  useEffect(() => {
      dispatch(loadProviderMaterialsAll());
  }, [])


  return (
    <React.Fragment>
       <Header
        color="transparent"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
      />
        <div className={classes.container}>
         <Grid container>
            <Grid item xs={12} sm={12} md={6}>
              <h1 className={classes.title}>Materials</h1>
            </Grid>
          </Grid>
        </div>
        <Parallax small filter image={backgroundImage} />
        <div className={classNames(classes.main, classes.mainRaised)}>
    
              <FilteringSection />
              <MaterialsList  
               history={props.history}
            
              />

          </div>
        
    </React.Fragment>
  );
};

export default MaterialsListPage;
