import React from "react";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  makeStyles,
  Slide,
} from "@material-ui/core";

import { Add, Close } from "@material-ui/icons";

import { Field, reduxForm } from "redux-form";

import { renderTextField } from "components/ReduxInput/ReduxInput";
import { useDispatch, useSelector } from "react-redux";
import { addArchitectDesign } from "features/architects/architectDesignsSlice";
import { loadArchitectDesigns } from "features/architects/architectDesignsSlice";

import ImageUploader from "components/ImageUpload/ImageUploader";

const useStyles = makeStyles((theme) => ({
  modal: {
    width: "55%",
    padding: 50,
    zIndex: "1302",
  },

  textField: {
    margin: 20,
    marginTop: "20px",
    width: "80%",
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let AddDesignForm = reduxForm({
  form: "addDesign",
});

AddDesignForm = (props) => {
  const { handleSubmit, pristine, reset, submitting, classes } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Grid container>
          <Grid item md={8}>
            <Grid container justify="center">
              <Grid item xs={12} sm={12} md={12}>
                <Field
                  name="title"
                  component={renderTextField}
                  label="Design Title"
                  className={classes.textField}
                />
              </Grid>
              <Grid item xs={12} sm={12} md={12}>
                <Field
                  name="description"
                  component={renderTextField}
                  className={classes.textField}
                  label="Short Description About the Design"
                  multiline={true}
                  variant="outlined"
                />
              </Grid>
            </Grid>
          </Grid>
          <Grid
                item
                md={3}
                justify="center"
                alignContent="center"
                alignItems="center"
              >
                <ImageUploader />
              </Grid>
        </Grid>

        <Button type="submit" color="primary" size="large">
          Add Design
        </Button>
      </form>
    </div>
  );
};

AddDesignForm = reduxForm({
  form: "createProject",
})(AddDesignForm);

export default function AddDesignModal(props) {
  const classes = useStyles();
  const [designModal, setDesignModal] = React.useState(false);

  const architectDetails = useSelector((state) => state.architectDetails);
  const { user } = useSelector((state) => state.user);

  const dispatch = useDispatch();
  const submit = (values) => {
    dispatch(
      addArchitectDesign(
        values,
        architectDetails.architectDetails[0].id,
        user.id,
        () => {
          dispatch(
            loadArchitectDesigns(architectDetails.architectDetails[0].id)
          );
          setDesignModal(false);
        }
      )
    );
  };

  return (
    <React.Fragment>
      {architectDetails !== null &&
      architectDetails.architectDetails !== null &&
      user.id === architectDetails.architectDetails[0].user_id ? (
        <React.Fragment>
          <Grid container justify="center">
            <Grid item>
              <Button
                variant="outlined"
                color="primary"
                onClick={() => setDesignModal(true)}
              >
                <Add color="primary" fontSize="default" />
                Add Design
              </Button>
            </Grid>
          </Grid>

          <Dialog
            classes={{
              root: classes.center,
              paper: classes.modal,
            }}
            open={designModal}
            maxWidth="lg"
            TransitionComponent={Transition}
            keepMounted
            onClose={() => setDesignModal(false)}
            aria-labelledby="classic-modal-slide-title"
            aria-describedby="classic-modal-slide-description"
          >
            <DialogTitle
              id="classic-modal-slide-title"
              disableTypography
              className={classes.modalHeader}
            >
              <Grid container justify="space-between">
                <Grid item>
                  <h4 className={classes.modalTitle}>Add Your Design</h4>
                </Grid>
                <Grid item>
                  <IconButton
                    className={classes.modalCloseButton}
                    key="close"
                    aria-label="Close"
                    color="inherit"
                    onClick={() => setDesignModal(false)}
                  >
                    <Close className={classes.modalClose} />
                  </IconButton>
                </Grid>
              </Grid>
            </DialogTitle>
            <DialogContent
              id="classic-modal-slide-description"
              className={classes.modalBody}
            >
              <AddDesignForm onSubmit={submit} classes={classes} />
            </DialogContent>
          </Dialog>
        </React.Fragment>
      ) : null}
    </React.Fragment>
  );
}
