import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  makeStyles,
  Slide,
} from "@material-ui/core";

import { Add, Close } from "@material-ui/icons";

import { Field, reduxForm } from "redux-form";

import { renderTextField } from "components/ReduxInput/ReduxInput";
import { useDispatch, useSelector } from "react-redux";
import { addContractorDesign } from "features/contractors/contractorDesignsSlice";
import { loadContractorDesigns } from "features/contractors/contractorDesignsSlice";
import { renderRadioButton } from "components/ReduxInput/ReduxInput";
import Slider from "@material-ui/core/Slider";

const useStyles = makeStyles((theme) => ({
  modal: {
    width: "55%",
    padding: 50,
  },

  textField: {
    margin: 20,
    marginTop: "20px",
    width: "80%",
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let AddDesignForm = reduxForm({
  form: "addDesign",
});

AddDesignForm = (props) => {
  const {
    handleSubmit,
    pristine,
    reset,
    submitting,
    classes,
    budgetValue,
    handleBudgetChange,
  } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Grid container justify="center">
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="title"
              component={renderTextField}
              label="Project Title"
              className={classes.textField}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="preference"
              component={renderRadioButton}
              label="Project Preference"
              className={classes.textField}
              radioValues={[
                "remodeling",
                "interior",
                "landscape",
                "residential",
                "commercial",
                "public",
              ]}
            />
          </Grid>

          <Grid container justify="center">
            <Grid item={0}>{budgetValue[0] * 50000}ETB - </Grid>
            <Grid item={0}>{budgetValue[1] * 50000}ETB</Grid>
          </Grid>

          <Slider value={budgetValue} onChange={handleBudgetChange} />

          <Grid container justify="space-between">
            <Grid item={0}>0</Grid>
            <Grid item={0}>5,000,000</Grid>
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="completion"
              component={renderRadioButton}
              label="Project Completion"
              className={classes.textField}
              radioValues={[
                "Completed",
                "Under_construction",
                "Not_started",
                "Canceled",
              ]}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About the Design"
              multiline={true}
              variant="outlined"
            />
          </Grid>
          <Button type="submit" color="primary" size="large">
            Add  Project
          </Button>
        </Grid>
      </form>
    </div>
  );
};

AddDesignForm = reduxForm({
  form: "createProject",
})(AddDesignForm);

export default function AddDesignModal(props) {
  const classes = useStyles();
  const [designModal, setDesignModal] = React.useState(false);

  const [budgetValue, setBudgetValue] = useState([0, 100]);

  const contractorDetails = useSelector((state) => state.contractorDetails);
  const { user } = useSelector((state) => state.user);

  const handleBudgetChange = (event, newValue) => {
    setBudgetValue(newValue);
    console.log("budget Value", budgetValue);
  };

  const dispatch = useDispatch();
  const submit = (values) => {
    const val = { ...values, budgetValue: budgetValue };
    dispatch(
      addContractorDesign(
        val,
        contractorDetails.contractorDetails[0].id,
        user.id,
        () => {
          dispatch(
            loadContractorDesigns(contractorDetails.contractorDetails[0].id)
          );
          setDesignModal(false);
        }
      )
    );
  };

  return (
    <React.Fragment>
      {contractorDetails !== null &&
      contractorDetails.contractorDetails !== null &&
      user.id === contractorDetails.contractorDetails[0].user_id ? (
        <React.Fragment>
          <Grid container justify="center">
            <Grid item>
              <Button
                variant="outlined"
                color="primary"
                onClick={() => setDesignModal(true)}
              >
                <Add color="primary" fontSize="default" />
                Add Previous Project
              </Button>
            </Grid>
          </Grid>

          <Dialog
            classes={{
              root: classes.center,
              paper: classes.modal,
            }}
            open={designModal}
            maxWidth="lg"
            TransitionComponent={Transition}
            keepMounted
            onClose={() => setDesignModal(false)}
            aria-labelledby="classic-modal-slide-title"
            aria-describedby="classic-modal-slide-description"
          >
            <DialogTitle
              id="classic-modal-slide-title"
              disableTypography
              className={classes.modalHeader}
            >
              <Grid container justify="space-between">
                <Grid item>
                  <h4 className={classes.modalTitle}>Add Your Design</h4>
                </Grid>
                <Grid item>
                  <IconButton
                    className={classes.modalCloseButton}
                    key="close"
                    aria-label="Close"
                    color="inherit"
                    onClick={() => setDesignModal(false)}
                  >
                    <Close className={classes.modalClose} />
                  </IconButton>
                </Grid>
              </Grid>
            </DialogTitle>
            <DialogContent
              id="classic-modal-slide-description"
              className={classes.modalBody}
            >
              <AddDesignForm
                onSubmit={submit}
                classes={classes}
                budgetValue={budgetValue}
                handleBudgetChange={handleBudgetChange}
              />
            </DialogContent>
          </Dialog>
        </React.Fragment>
      ) : null}
    </React.Fragment>
  );
}
