import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { Grid, Paper, StepContent } from "@material-ui/core";
import theme from "assets/jss/theme";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "10%",
    position: "fixed",
    backgroundColor: "transparent",
    // bottom:0,
    right: "30vw",
    padding: 0,
    top: "88vh",
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ["Select campaign settings", "Create an ad group", "Create an ad", "", ''];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return "Select campaign settings...";
    case 1:
      return "What is an ad group anyways?";
    case 2:
      return "This is the bit I really care about!";
    default:
      return "Unknown step";
  }
}

export default function Steppers() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());
  const steps = getSteps();

  const isStepOptional = (step) => {
    return step === 1;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <Grid container >
    <Stepper
      activeStep={activeStep}
      className={classes.root}
      nonLinear={false}
      // connector={false}
    > 
   
      {steps.map((label, index) => {
        const stepProps = {};
        const labelProps = {};
        if (isStepOptional(index)) {
          labelProps.optional = (
            <Typography key={index} variant="caption">Optional</Typography>
          );
        }
        if (isStepSkipped(index)) {
          stepProps.completed = false;
        }
        return (
            
          <Step key={index}>
            {/* <StepLabel  style={{backgroundColor:'white', padding:0, paddingLeft:7, paddingTop:7, paddingBottom:7,borderRadius:'50%'}} /> */}
            {/* <StepContent style={{backgroundColor:'red', padding:0, paddingLeft:7, paddingTop:7, paddingBottom:7,borderRadius:'50%'}}/> */}
            {/* <Paper
              style={{
                width: 50,
                height: 50,
                paddingTop: 5,
                paddingLeft: 5,
                // paddingBottom: 5,
                // paddingRight: 5,
                borderRadius: "50%",
              }}
            >
              <Paper
                style={{
                  width: 40,
                  height: 40,
                  margin:0,
                  backgroundColor: "blue",
                  
                  borderRadius: "50%",
                }}
              />
            </Paper> */}
          
              { index === 1 ? 
                  <Grid item style={{backgroundColor:'white', padding:7, borderRadius:'50%',marginRight:20}}>
                <Paper
                style={{
                  width: 30,
                  height: 30,
                  marginRight:0,
                  backgroundColor: theme.palette.primary.main,
                  
                  borderRadius: "50%",
                }}
              />
                </Grid> :
                 <Grid item style={{backgroundColor:'white', padding:7, borderRadius:'50%',marginRight:20}}>
                 <Paper
                 style={{
                   width: 30,
                   height: 30,
                   margin:0,
                   backgroundColor: 'grey',
                   
                   borderRadius: "50%",
                 }}
               />
                 </Grid> 
              }  
            
          </Step>
        );
      })}
      
    </Stepper>
    </Grid>
  );
}
