import React from "react";

import profile from "assets/img/faces/christian.jpg";
import classNames from "classnames";
import {
  Box,
  Grid,
  makeStyles,
  Typography,
  IconButton,
} from "@material-ui/core";
import { useSelector } from "react-redux";

import { title } from "assets/jss/commons";

import { Facebook, LinkedIn, Twitter } from "@material-ui/icons";

import imagesStyle from "assets/jss/imagesStyles";
import theme from "assets/jss/theme";
import { Grade, NaturePeopleSharp, WbIridescent } from "@material-ui/icons";
import ProfileEditModal from "./ProfileEditModal";

const useStyles = makeStyles({
  profileContainer: {
    marginTop: "50px",
    padding: "50px",
    borderRadius: "6px",
    backgroundColor: theme.palette.secondary.light,
  },
  description: {
    margin: "1.071rem auto 0",
    maxWidth: "600px",
    color: "#999",
    textAlign: "center !important",
  },
  ...imagesStyle,
  customImage: {
    width: "50%",
    height: "auto",
    marginLeft: "40px",
  },
  title: {
    ...title,
    display: "inline-block",
    position: "relative",
    marginTop: "30px",
    marginBottom: "0",
    minHeight: "32px",
    textDecoration: "none",
  },
  socials: {
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
    color: "#999",
  },
  descriptionContainer: {
    textAlign: "flex-start",
    padding: "20px 50px",
  },
  descriptionText: {
    color: theme.palette.primary.main,
    fontWeight: "bold",
    textAlign: "center",
  },
  bulletsContainer: {
    marginTop: "40px",
    marginBottom: "40px",
  },
});

const ContractorDescriptionSection = () => {
  const contractorDetails = useSelector((state) => state.contractorDetails);
  const { user } = useSelector((state) => state.user);
  const classes = useStyles();

  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid,
    classes.customImage
  );
  if (
    contractorDetails.contractorDetails !== null &&
    contractorDetails.contractorDetails.length > 0
  ) {
    const contractor = contractorDetails.contractorDetails[0];
    return (
      <div>
        <div className={classes.profileContainer}>
          {user.id === contractor.user_id ? (
            <ProfileEditModal contractorId={contractor.id} />
          ) : null}
          <Grid container justify="flex-start" alignItems="center" spacing={10}>
            <Grid item md={4}>
              <div>
                <img src={profile} alt="..." className={imageClasses} />
              </div>
            </Grid>
            <Grid item md={4}>
              <div className={classes.name}>
                <h2 className={classes.title}>{contractor.name}</h2>
                <h4>Contractor Type: {contractor.contractor_type}</h4>

                <h4>
                  {contractor.contractor_type === "individual"
                    ? contractor.nationality + "n"
                    : 'Tin Number: ' + contractor.tin_number}{" "}
                </h4>

                <div className={classes.socialLine}>
                  <IconButton>
                    <Twitter color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <Facebook color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <LinkedIn color="primary" className={classes.socialIcons} />
                  </IconButton>
                </div>
              </div>
            </Grid>
            <Grid item md={4}>
              <div className={classes.name}>
                <h5>
                  {contractor.civil ? "Civil," : null}{" "}
                  {contractor.electrica ? "Electrical," : null}{" "}
                  {contractor.mechanical ? "Mechanical," : null}{" "}
                  {contractor.hvac ? "HVAC" : null}
                </h5>
                <h4>{contractor.school} </h4>

                <div>
                  {" "}
                  {contractor.contractor_type === "individual" ? (
                    <p>Graduate of: {contractor.graduate_year}</p>
                  ) : null}{" "}
                </div>
                <h4>Location: {contractor.location} </h4>
                <h4>Website: {contractor.website} </h4>
              </div>
            </Grid>
          </Grid>
          <div className={classes.description}>
            <Typography>{contractor.short_description}</Typography>
          </div>
        </div>

        <Grid
          container
          spacing={3}
          justify="center"
          alignItems="center"
          className={classes.bulletsContainer}
        >
          <Grid item sm={2} className={classes.descriptionContainer}>
            <Box
              borderRight={1}
              borderColor="primary"
              className={classes.descriptionContainer}
            >
              <WbIridescent fontSize="large" />
              <Typography variant="body1" className={classes.descriptionText}>
                5 Designs
              </Typography>
            </Box>
          </Grid>
          <Grid item sm={2} className={classes.descriptionContainer}>
            <Box
              borderRight={1}
              borderColor="primary"
              className={classes.descriptionContainer}
            >
              <NaturePeopleSharp fontSize="large" />
              <Typography variant="body1" className={classes.descriptionText}>
                10 Clients
              </Typography>
            </Box>
          </Grid>
          <Grid item sm={2} className={classes.descriptionContainer}>
            <Box
              borderRight={1}
              borderColor="primary"
              className={classes.descriptionContainer}
            >
              <Grade fontSize="large" />
              <Typography variant="h6" className={classes.descriptionText}>
                {" "}
                4.5
              </Typography>
            </Box>
          </Grid>
          <Grid item sm={2} className={classes.descriptionContainer}>
            <Box className={classes.descriptionContainer}>
              <WbIridescent fontSize="large" />
              <Typography>More</Typography>
            </Box>
          </Grid>
        </Grid>
      </div>
    );
  } else {
    return null;
  }
};

export default ContractorDescriptionSection;
