import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { Divider, Grid, TextField, Typography } from "@material-ui/core";
import theme from "assets/jss/theme";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export default function FilteringSection() {
  const categories = [
    {
      value: "category1",
      label: "Category One",
    },
    {
      value: "category2",
      label: "Category Two",
    },
    {
      value: "category3",
      label: "Category Three",
    },
    {
      value: "category4",
      label: "Category Four",
    },
    {
      value: "category5",
      label: "Category five",
    },
    {
      value: "category6",
      label: "Category Six",
    },
  ];

  const availabilities = [
    {
      value: "available",
      label: "Available",
    },
    {
      value: "not_available",
      label: "Not Available",
    },
  ];

  const classes = useStyles();
  const [category, setCategory] = React.useState("");
  const [available, setAvailable] = useState();

  const handleCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleAvailabilityChange = (event) => {
    setAvailable(event.target.value);
  };

  const RenderDropDown = (props) => {
    const {
      title,
      selectedOption,
      filteringOptions,
      handleFilterChange,
    } = props;

    return (
      <FormControl >
        <InputLabel id="demo-simple-select-label">{title}</InputLabel>
        <Select
          labelId="category-dropdown-label-id"
          id="category-dropdown-id"
          value={selectedOption}
          onChange={handleFilterChange}
        >
          {filteringOptions.map((option) => (
            <MenuItem value={option.value}>{option.label}</MenuItem>
          ))}
        </Select>
      </FormControl>
    );
  };

  return (
    <Grid container justify="center" alignItems="flex-end" spacing={2}>
    <Grid item>
         <RenderDropDown
          title="Category"
          selectedOption={category}
          filteringOptions={categories}
          handleFilterChange={handleCategoryChange}
        />
    </Grid>
       <Grid item >
           <Divider  orientation='vertical' style={{backgroundColor:theme.palette.primary.light, width:1, height:50}}/>
       </Grid>
     

      <Grid item>
        <TextField label="location" />{" "}
      </Grid>
      <Grid item >
           <Divider  orientation='vertical' style={{backgroundColor:theme.palette.primary.light, width:1, height:50}}/>
       </Grid>
      <Grid item>
        <Grid container direction="column" alignItems="center">
          <Grid item>
            <Grid item>
              <Typography>Price Range</Typography>{" "}
            </Grid>
          </Grid>
          <Grid item>
            <Grid container alignItems='center' spacing={1}>
              <Grid item>
                <TextField variant='outlined' type="number" label="Starting Price" />{" "}
              </Grid>
              <Grid item>
                  <Typography variant='h5'>-</Typography>
              </Grid>
              <Grid item>
                <TextField variant='outlined' type="number" label="End Price" />{" "}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item >
           <Divider  orientation='vertical' style={{backgroundColor:theme.palette.primary.light, width:1, height:50}}/>
       </Grid>
      <Grid item>
           <RenderDropDown
        title="Availability"
        selectedOption={available}
        filteringOptions={availabilities}
        handleFilterChange={handleAvailabilityChange}
      />
      </Grid>
     

      {/* <Grid item>Category</Grid> */}
    </Grid>
  );
}
