const PreferencesDND = (props) => {
  const { initialValues,title } = props;

  const [items, setItems] = useState(initialValues);

  const onDragEnd = (result) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }

    const itemList = reorder(
      items,
      result.source.index,
      result.destination.index
    );

    setItems(itemList);
  };

  return (
    <div 
    style={{
      backgroundColor:'#ebecf0',
      paddingTop:20,
      paddingBottom:20
      }}>
      <Typography variant='h5' color='primary' style={{marginLeft:20}}>{title}</Typography>
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId="droppable">
        {(provided, snapshot) => (
          <div
            {...provided.droppableProps}
            ref={provided.innerRef}
            style={getListStyle(snapshot.isDraggingOver)}
          >
            {items.map((item, index) => (
                 <Draggable key={item.id} draggableId={item.id} index={index}>
                {(provided, snapshot) => (
                  <div
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    style={getItemStyle(
                      snapshot.isDragging,
                      provided.draggableProps.style
                    )}
                  >
                    <Grid container alignItems='center' >
                     
                      <Grid item md={6}> 
                          <img src={item.image}  width='100%' height='auto'/>
                      </Grid>
                      <Grid item md={5}>
                        <Typography  color='textPrimary' style={{textAlign:'center'}}>{item.content}</Typography>
                      </Grid>
                      <Grid item md={1}>
                        <DragIndicatorIcon />
                      </Grid>
                    
                    </Grid>
                  </div>
                )}
              </Draggable>
             
            ))}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </DragDropContext>
    </div>
  );
};


