CreateProjectForm = reduxForm({
  form: "createProject",
})(CreateProjectForm);

export default function CreateProjectPage(props) {
  const classes = useStyles();

  useEffect(() => {
    dispatch(loadArchitects());
    dispatch(loadProviders());
    dispatch(loadContractors());
    dispatch(loadProviderMaterialsAll());
  }, []);

  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const submit = (values) => {
    if (user.user !== null) {
      values = { ...values, user_id: user.user.id };
    }

    values = { ...values, budgetValue: budgetValue };

    dispatch(addProject(values, () => props.history.push("/recommendations")));
  };

  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  setTimeout(function () {
    setCardAnimation("");
  }, 700);

  const { providerMaterials } = useSelector((state) => state.providerMaterials);

  useEffect(() => {
    dispatch(loadProviderMaterialsAll());
    console.log("providerMaterials are", providerMaterials);
  }, []);
  const [budgetValue, setBudgetValue] = useState([0, 100]);
  const handleBudgetChange = (event, newValue) => {
    setBudgetValue(newValue);
    console.log("budget Value", budgetValue);
  };
  return (
    <div>
      <Header
        color="transparent"
        brand="Nidf"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white",
        }}
        // {...rest}
      />

      <Parallax small filter image={imageBackground} />
      <div className={classNames(classes.main, classes.mainRaised)}>
        <Grid container justify="flex-start">
          <Grid item xs={12} sm={12} md={11}>
            <Typography className={classes.title} variant="h3">
              Create Your Project
            </Typography>

            <CreateProjectForm
              onSubmit={submit}
              classes={classes}
              budgetValue={budgetValue}
              handleBudgetChange={handleBudgetChange}
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
