import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { makeStyles } from "@material-ui/core/styles";

import { Field, reduxForm } from "redux-form";

import image from "assets/img/backgrounds/bg4.jpg";

import { signup } from "features/auth/userSlice";
import { container } from "assets/jss/commons";
import { renderTextField } from "components/ReduxInput/ReduxInput";
import {
  Box,
  Button,
  CardActions,
  Grid,
  IconButton,
  Typography,
} from "@material-ui/core";
import Footer from "components/Footer/Footer";
import { Facebook, LinkedIn, Twitter } from "@material-ui/icons";
import { renderCheckbox } from "components/ReduxInput/ReduxInput";
import { Link } from "react-router-dom";
import Header from "components/Header/Header";
import HeaderLinks from "components/Header/HeaderLinks";
import zIndex from "@material-ui/core/styles/zIndex";

const useStyles = makeStyles((theme) => ({
  container: {
    ...container,
    zIndex: "2",
    position: "relative",
    paddingTop: "30vh",
    color: "#FFFFFF",
    paddingBottom: "200px",
  },
  pageHeader: {
    minHeight: "100vh",
    height: "auto",
    display: "inherit",
    position: "relative",
    margin: "0",
    padding: "0",
    border: "0",
    alignItems: "center",
    "&:before": {
      background: "rgba(0, 0, 0, 0.5)",
    },
    "&:before,&:after": {
      position: "absolute",
      zIndex: "1",
      width: "100%",
      height: "100%",
      display: "block",
      left: "0",
      top: "0",
      content: '""',
    },
    "& footer li a,& footer li a:hover,& footer li a:active": {
      color: "#FFFFFF",
    },
    "& footer": {
      position: "absolute",
      bottom: "0",
      width: "100%",
    },
  },
  cardContainer: {
    backgroundColor: "white",
    marginTop: "10%",
    borderRadius: 5,
    justifyContent: "center",
  },
  cardHidden: {
    opacity: "0",
    transform: "translate3d(0, -60px, 0)",
  },
  cardHeader: {
    paddingBottom: "50px",
    paddingTop: "20px",
    textAlign: "center",
    backgroundColor: theme.palette.primary.dark,
    borderRadius: "3px",
    marginLeft: "15px",
    marginRight: "15px",
    marginTop: "-50px",
    border: "0",
    marginBottom: "0",
    position: "absolute",
    width: "37%",
  },
  cardBody: {
    backgroundColor: "transparent",
    marginTop: -100,
    padding: "0.9375rem 1.875rem",
    flex: "1 1 auto",
    borderRadius: 15,
    paddingTop: "150px",
    paddingBottom: "50px",
  },
  cardFooter: {
    justifyContent: "center",
  },
  headerTitle: {
    color: "white",
  },
  socialIcons: {
    maxWidth: "24px",
    marginTop: "0",
    width: "100%",
    transform: "none",
    left: "0",
    top: "0",
    height: "100%",
    lineHeight: "41px",
    fontSize: "20px",
    color: "white",
  },
  divider: {
    marginTop: "30px",
    marginBottom: "0px",
    textAlign: "center",
    color: theme.palette.primary.main,
  },
  socialLine: {
    marginTop: "1rem",
    textAlign: "center",
    padding: "0",
  },
  inputIconsColor: {
    color: "red",
  },
  textField: {
    marginTop: 10,
    marginBottom: 20,
    marginRight: 20,
    color:'#000',
    zIndex:1302
    // textDecorationColor:'white'
  },
  textField2: {
    margin: 20,
    marginTop: "20px",
    width: "100%",
    zIndex:1302
  },
}));

let SignupForm = reduxForm({
  form: "signup",
});

SignupForm = (props) => {
  const { handleSubmit,  classes } = props;

  const errorMessage = useSelector((state) => state.user.errorMessage);

  return (
    <form onSubmit={handleSubmit}>
      <Grid container direction='column'>
        <Grid item>
           <Field
        name="email"
        component={renderTextField}
        label="Email"
        fullWidth={true}
        className={classes.textField}
      />
        </Grid>
     
        <Grid item>
          <Field
        name="password"
        component={renderTextField}
        label="password"
        type="password"
        fullWidth={true}
        className={classes.textField}
      />
          </Grid>
          <Grid item>
            <Field
        name="architect"
        component={renderCheckbox}
        label="I am an Architect"
        className={classes.textField2}
      />
          </Grid>
          <Grid item>
           <Field
        name="provider"
        component={renderCheckbox}
        className={classes.textField2}
        label="I am a Material Provider"
      />
          </Grid>
     <Grid>
        <Field
        name="contractor"
        component={renderCheckbox}
        className={classes.textField2}
        label="I am a Contractor"
      />
     </Grid>
     
      </Grid>
      

      {errorMessage ? <div style={{ color: "red" }}>{errorMessage}</div> : null}

      <CardActions className={classes.cardFooter}>
        <Button type="submit" color="primary" size="large">
          Sign Up
        </Button>
      </CardActions>
    </form>
  );
};

SignupForm = reduxForm({
  form: "signup",
})(SignupForm);

export default function SignupPage(props) {
  const classes = useStyles();

  const dispatch = useDispatch();

  const { user } = useSelector((state) => state.user);
  console.log(user);

  const [cardAnimaton, setCardAnimation] = useState("cardHidden");

  setTimeout(function () {
    setCardAnimation("cardContainer");
  }, 700);

  const submit = (values) => {
    dispatch(signup(values, () => props.history.push("/")));
  };

  return (
    <React.Fragment>
         <Header
        color="transparent"
        brand="NIDF"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white",
        }}
      />
<div
      className={classes.pageHeader}
      style={{
        backgroundImage: "url(" + image + ")",
        backgroundSize: "cover",
        backgroundPosition: "top center",
      }}
    >
      <div className={classes.container}>
        <Grid container justify="flex-end">
          <Grid item xs={5} sm={5} md={5}>
            <Box className={classes[cardAnimaton]}>
              <Box boxShadow={10} className={classes.cardHeader}>
                <Typography className={classes.headerTitle} variant="body1">
                  Signup
                </Typography>
                <div className={classes.socialLine}>
                  <IconButton>
                    <Twitter color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <Facebook color="primary" className={classes.socialIcons} />
                  </IconButton>
                  <IconButton>
                    <LinkedIn color="primary" className={classes.socialIcons} />
                  </IconButton>
                </div>
              </Box>

              <Box className={classes.cardBody}>
                <p className={classes.divider}>Or Be Classical</p>
                <SignupForm
                  user={user}
                  onSubmit={submit}
                  classes={classes}
                  errorMessage={user ? user.errorMessage : null}
                />
                 <Typography variant='body2' component={Link} to='/login' className={classes.divider}>Already Have an Account? Sign In Here</Typography>
              
              </Box>
            </Box>
          </Grid>
        </Grid>
      </div>
      {/* <Footer /> */}
    </div>
    </React.Fragment>
    
  );
}
