import { Button, Card, Grid, Typography } from "@material-ui/core";
import {
  Close,
  FiberManualRecord,
  RadioButtonChecked,
  RadioButtonUnchecked,
} from "@material-ui/icons";
import { makeStyles } from "@material-ui/styles";
import React from "react";
import Box from "@material-ui/core/Box";
import LinearProgress from "@material-ui/core/LinearProgress";

import PropTypes from "prop-types";

import CircularProgress from "@material-ui/core/CircularProgress";

const useStyles = makeStyles((theme) => ({
  tasksSectionContainer: {
    backgroundColor: "white",
    width: "100%",
    padding: 20,
  },
  percentage: {
    backgroundColor: theme.palette.primary.main,
    color: "white",
    padding: "10px",
    borderRadius: 10,
    fontSize: 10,
    marginLeft: 10,
    marginRight: 10,
  },
  taskListContainer: {
    padding: 20,
  },
  taskContainer: {},
  circleIcon: {
    fontSize: 25,
    //   fontWeight:10
  },
  root: {
    width: "100%",
    marginBottom: 10,
  },
  contribute:{
      backgroundColor:theme.palette.primary.light,
      color:'white',
      paddingRight:20,
      paddingLeft:20,
      borderRadius:10
  }
}));

const taskList = [
  {
    name: "taskOne",
    color: "#f1e0b8",
  }
];

const Tasks = (props) => {
  const { classes } = props;

  const TasksHeader = () => {
    return (
      <Grid container justify="space-between">
        <Grid item>
          <Typography style={{ fontWeight: "bold" }}>Tasks</Typography>
        </Grid>

        <Grid item>
          <Grid container>
            <Grid item className={classes.percentage}>
              percent
            </Grid>
            <Grid item className={classes.percentage}>
              percent
            </Grid>
            <Grid item className={classes.percentage}>
              percent
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    );
  };

  const TasksList = () => {
    return (
      <Grid container direction="column" className={classes.taskListContainer}>
        {taskList.map((task, index) => (
          <Grid item key={index}>
            <Grid
              container
              justify="center"
              alignContent="center"
              alignItems="center"
              className={classes.taskContainer}
            >
              <Grid item md={1}>
                {index < 2 ? (
                  <RadioButtonUnchecked
                    className={classes.circleIcon}
                    style={{ color: task.color }}
                  />
                ) : (
                  <FiberManualRecord
                    className={classes.circleIcon}
                    style={{ color: task.color }}
                  />
                )}
              </Grid>
              <Grid item md={11}>
                <Grid container justify="space-between">
                  <Grid item>
                    <Typography variant="p">Select Date</Typography>
                    <Typography style={{ fontWeight: "bold" }}>
                      Your Tasks are Listed here
                    </Typography>
                    <Typography variant="caption">alert me!</Typography>
                  </Grid>
                  <Grid item>Tue | 2020</Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        ))}
      </Grid>
    );
  };

  return (
    <Grid container direction="column" spacing={2}>
      <Grid item>
        <TasksHeader />
      </Grid>
      <Grid item>
        <TasksList />
      </Grid>
    </Grid>
  );
};

const Financial = (props) => {
  const { classes } = props;

  const FinancialHeader = () => {
    return (
      <Grid container justify="space-between">
        <Grid item>
          <Typography style={{ fontWeight: "bold" }}>Financial</Typography>
        </Grid>
        <Grid item className={classes.percentage}>
          percent
        </Grid>
      </Grid>
    );
  };

  function CircularProgressWithLabel(props) {
    return (
      <Box position="relative" display="inline-flex">
        <CircularProgress variant="determinate" {...props} size={100} />
        <Box
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <Typography
            variant="caption"
            component="div"
            color="textSecondary"
          >{`${Math.round(props.value)}%`}</Typography>
        </Box>
      </Box>
    );
  }

  CircularProgressWithLabel.propTypes = {
    /**
     * The value of the progress indicator for the determinate variant.
     * Value between 0 and 100.
     */
    value: PropTypes.number.isRequired,
  };

  const CircularProgressSection = () => {
    return (
      <Grid container direction="column">
        <Grid item>
          {/* <CircularProgress value={75} variant="determinate"/> */}
          <CircularProgressWithLabel value={0} />

         
        </Grid>
        <Grid item>
        <Typography variant='caption'>Cost 0 ETB</Typography>
        </Grid>
      </Grid>
    );
  };

  const HorizontalProgresses = () => {
    const progress = [0, 0, 0];

    return (
      <Grid container direction="column">
        <Grid item style={{ width: "200px", marginBottom: 10 }}>
            <Grid container direction='column'>
                <Grid item>
                    <Grid container style={{marginBottom:10, marginTop:10}} justify='center'>
                        <Grid item >
                           <Typography style={{color:'grey', fontSize:13}}>Working Hours:{' '}</Typography> 
                        </Grid>
                        <Grid item >
                        <Typography style={{color:'green', fontSize:13, fontWeight:'bold'}}>0</Typography> 
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <LinearProgress variant="determinate" value={progress[0]} />
                </Grid>
            </Grid>
        
        </Grid>
        <Grid item style={{ width: "200px", marginBottom: 10 }}>
            <Grid container direction='column'>
                <Grid item>
                    <Grid container style={{marginBottom:10 ,marginTop:10}} justify='center'>
                        <Grid item >
                           <Typography style={{color:'grey', fontSize:13}}>Cash on hand:{' '}</Typography> 
                        </Grid>
                        <Grid item >
                        <Typography color='secondary' style={{ fontSize:13, fontWeight:'bold'}}>0</Typography> 
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <LinearProgress variant="determinate" value={progress[1]} />
                </Grid>
            </Grid>
        
        </Grid>
        <Grid item style={{ width: "200px", marginBottom: 10 }}>
            <Grid container direction='column'>
                <Grid item>
                    <Grid container style={{marginBottom:10, marginTop:10}} justify='center'>
                        <Grid item >
                           <Typography style={{color:'grey', fontSize:13}}>Efficiency:{' '}</Typography> 
                        </Grid>
                        <Grid item >
                        <Typography style={{color:'green', fontSize:13, fontWeight:'bold'}}>0</Typography> 
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <LinearProgress variant="determinate" value={progress[2]} />
                </Grid>
            </Grid>
        
        </Grid>
      </Grid>
    );
  };
  const Contribute = () => {
    return <Button className={classes.contribute}>Add Task</Button>;
  };

  return (
      <div style={{borderLeftWidth:'5px', borderLeftColor:'black'}} borderLeft={5} borderColor='black'>
          <Grid
      container
      direction="column"
      spacing={3}
      justify="center"
      alignItems="center"
      alignContent="center"
    >
      <Grid item>
        <FinancialHeader />
      </Grid>
      <Grid item>
        <CircularProgressSection />
      </Grid>
      <Grid item>
        <HorizontalProgresses />
      </Grid>
      <Grid item>
        <Contribute />
      </Grid>
    </Grid> 
      </div>
   
  );
};
export default function TasksSection() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Card className={classes.tasksSectionContainer}>
        <Grid container>
          <Grid item md={9}>
            <Tasks classes={classes} />
          </Grid>
          <Grid item md={3}>
            <Financial classes={classes} />
          </Grid>
        </Grid>
      </Card>
    </React.Fragment>
  );
}
