import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const contractorDetailsSlice = createSlice({
    name: 'contractorDetails',
    initialState: {
        contractorDetails : null
    },
    reducers: {
        loadContractorDetailsSuccess: (state, action) => {
            state.contractorDetails=action.payload
        }
    }
})

export default contractorDetailsSlice.reducer;

const {loadContractorDetailsSuccess} = contractorDetailsSlice.actions;

export const loadContractorDetails = (contractorId) => async dispatch => {
    try {
        const res = await api.get('/contractors?id='+contractorId);
        console.log("contractors Details",res.data)
        dispatch(loadContractorDetailsSuccess(res.data))
    } catch (e) {
        return console.log(e.message)
    }
}

export const editContractorDetails = (contractorId,values,callback) => async dispatch => {
    try {
        const res = await api.patch('/contractors/'+contractorId,values );
        console.log("contractors Details",res.data)
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}