import { loadArchitects } from 'features/architects/architectsSlice';
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ArchitectProfilePage from '../ArchitectProfilePage/ArchitectProfilePage';
import CreateArchitectProfile from '../CreateArchitectProfile/CreateArchitectProfile';
import MyProfile from './MyProfile';

export default function MyArchitectPage(props) {

    const {architects} = useSelector(state => state.architects);
    
    const user = useSelector(state => state.user)

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(
            loadArchitects()
        ) 
        
    }, [])

    return (
      
        architects !==null 
        
        ?
        
        architects.some((architect)=> architect.user_id === user.user.id) ? 
        <MyProfile architectId={architects.filter((architect)=>architect.user_id===user.user.id)[0].id} /> 
        : 
       <CreateArchitectProfile userId={user.user.id} history={props.history} />
        :
        <div>
            Loading
        </div>
    )
}
