import {createSlice} from '@reduxjs/toolkit';
import api from 'services/apiService';

const architectsSlice = createSlice({
    name: 'architects',
    initialState: {
        architects : null
    },
    reducers: {
        addArchitectsSuccess: (state, action) => {
            state.architects=action.payload
        }
    }
})

export default architectsSlice.reducer;

const {addArchitectsSuccess} = architectsSlice.actions;

export const loadArchitects = (callback) => async dispatch => {
    try {
        const res = await api.get('/architects');
        dispatch(addArchitectsSuccess(res.data));
    } catch (e) {
        return console.log(e.message)
    }
}

export const addArchitect = (values, userId,callback) => async dispatch => {
    const val= {...values,
        user_id:userId
    }
    try {
       await api.post('/architects',val);
       loadArchitects();
        callback();
    } catch (e) {
        return console.log(e.message)
    }
}
