const validate = (values) => {
  const errors = {};
  const requiredFields = ["name"];
  requiredFields.forEach((field) => {
    if (!values[field]) {
      errors[field] = "Required";
    }
  });
  if (
    values.email &&
    !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
  ) {
    errors.email = "Invalid email address";
  }
  return errors;
};

let EditDesignForm = reduxForm({
  form: "editDesign",
});

EditDesignForm = (props) => {
  const { handleSubmit, pristine, reset, submitting, classes } = props;

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Grid container justify="center">
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="title"
              component={renderTextField}
              label="Design Title"
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={12}>
            <Field
              name="description"
              component={renderTextField}
              className={classes.textField}
              label="Short Description About the Design"
              multiline={true}
              variant="outlined"
            />
          </Grid>
          <Button type="submit" color="primary" size="large">
            Update Design
          </Button>
        </Grid>
      </form>
    </div>
  );
};

EditDesignForm = reduxForm({
  form: "createProject",
})(EditDesignForm);

const ArchitectDesignsSection = (props) => {
  const { ...rest } = props;

  const classes = useStyles();

  const dispatch = useDispatch();

  const { architectDesigns } = useSelector((state) => state.architectDesigns);
  const { user } = useSelector((state) => state.user);

  const [selectedDesign, setSelectedDesign] = useState(null);
  const [open, setOpen] = useState(false);
  const [openEditDesign, setOpenEditDesign] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = (value) => {
    setOpen(false);
  };

  const handleOpenEditModal = () => {
    setOpenEditDesign(true);
  };
  const handleCloseEditModal = (value) => {
    setOpenEditDesign(false);
  };

  const EditSuccessful = () => {
    dispatch(loadArchitectDesigns(user.id));
    setOpenEditDesign(false);
  };

  const submit = (values) => {
    dispatch(
      //  (values,user.id, ()=>setDesignModal(false))
      editArchitectDesign(selectedDesign, values, () => EditSuccessful())
    );
  };

  return (
    <React.Fragment>
      <div className={classes.container}>
        <Grid container justify='center' spacing={5}>

        {architectDesigns !== null ? (
          architectDesigns.map((design,index) => (
            <Grid item key={design.id}  md={4}>
              <Dialog
                onClose={handleClose}
                aria-labelledby="simple-dialog-title"
                open={open}
              >
                <img src={imageList[index%3]} />
              </Dialog>

              <Dialog
                onClose={handleCloseEditModal}
                aria-labelledby="simple-dialog-title"
                open={openEditDesign}
                maxWidth="lg"
                classes={{ paper: classes.editModal }}
              >
                <EditDesignForm onSubmit={submit} classes={classes} />
              </Dialog>
              {/* <div className={classes.container}> */}
              
                <Card >
              
                  <CardMedia
                    onClick={handleClickOpen}
                    className={classes.cover}
                    image={imageList[index%3]}
                    title="Live from space album cover"
                  >
                    
                    <div className={classes.details}>
                        <Typography
                            style={{ color: "white" , opacity:1}}
                            component="h5"
                            variant="h5"
                          >
                            {design.title}
                          </Typography>
                          <Typography variant="body1" style={{ color: "white" }}>
                        {design.description}
                      </Typography>
                    </div>
                  </CardMedia>
                 
                  {/* <div className={classes.details}>
                    <CardContent className={classes.content}>
                      <Grid container direction="row" justify="space-between">
                        <Grid item>
                          <Typography
                            color="primary"
                            component="h5"
                            variant="h5"
                          >
                            {design.title}
                          </Typography>
                        </Grid>
                        {user.id === design.architect_id ? (
                          <Grid item>
                            <IconButton
                              className={classes.editButton}
                              onClick={handleOpenEditModal}
                              onClick={() => {
                                setSelectedDesign(design.id);
                                handleOpenEditModal();
                              }}
                            >
                              <Edit />
                            </IconButton>
                          </Grid>
                        ) : null}
                      </Grid>

                      <Typography variant="body1" style={{ color: "white" }}>
                        {design.description}
                      </Typography>
                    </CardContent>
                  </div> */}
                </Card>
              {/* </div> */}
            </Grid>
          ))
        ) : (
          <div>No Designs Found</div>
        )}
      </Grid>
      </div>
    </React.Fragment>
  );
};
export default ArchitectDesignsSection;
